<template>
  <div class="container-absolute"
       v-loading="saving">
    <div class="container-absolute-view">
      <div class="row-name-title">
        <div class="first-font">{{firstFont}}</div>
        <div class="name-title">{{viewData.informName}}</div>
        <el-button type="primary"
                   @click="$emit('close')">返回</el-button>
      </div>
      <el-form label-width="120px"
               ref="form"
               :model="form"
               :rules="rules">
        <!-- <div class="form-flex-box"> -->
          <!-- <el-form-item label="涉及行业">
            <el-input v-model="form.business"
                      class="form-edit-content"
                      clearable />
          </el-form-item> -->
          <!-- <el-form-item label="核查地点">
            <el-input v-model="form.add"
                      class="form-edit-content"
                      clearable />
          </el-form-item> -->
        <!-- </div> -->
        <div class="form-flex-box">
          <el-form-item label="核查详情"
                        class="width-100">
            <el-input v-model="form.checkDetails"
                      type="textarea"
                      placeholder="请输入排查出的主要违法违规行为和风险点、业务模式、规模和最新处置情况等内容。"
                      resize="none"
                      :autosize="{ minRows: 2, maxRows: 4 }"
                      clearable />
          </el-form-item>
        </div>
        <div class="form-flex-box">
          <el-form-item label="线索审核"
                        prop="verification">
            <el-select-mine v-model="form.verification"
                            :options="clueAuditOptions"
                            class="form-edit-content" />
          </el-form-item>
          <el-form-item label="处置措施">
            <el-select-mine v-model="form.disposalMeasures"
                            :options="disposeStepOptions"
                            class="form-edit-content" />
          </el-form-item>
          <el-form-item label="奖励情况"
                        prop="rewardStatus">
            <el-select-mine v-model="form.rewardStatus"
                            :options="awardSituationOptions"
                            class="form-edit-content" />
          </el-form-item>
          <template v-if="form.rewardStatus===awardDone">
            <el-form-item label="奖励金额(元)"
                          prop="rewardAmount">
              <el-input v-model="form.rewardAmount"
                        class="form-edit-content" />
            </el-form-item>
            <el-form-item label="奖励时间"
                          prop="rewardTime">
              <el-date-picker v-model="form.rewardTime"
                              :clearable="false"
                              type="datetime"
                              valueFormat="yyyy/MM/dd HH:mm:ss"
                              format="yyyy/MM/dd HH:mm:ss"
                              class="form-edit-content" />
            </el-form-item>
          </template>
        </div>
      </el-form>
    </div>
    <div class="bottom-operate">
      <el-button type="primary"
                 @click="submit">提交</el-button>
      <el-button @click="reset">重置</el-button>
    </div>
  </div>
</template>
<script>
import * as CONSTANTS from '@/constants/common'
import { $rNumberValidate } from '@/utils/validFuns'
export default {
  name: 'ReportComplainInspect',
  props: {
    viewData: {
      type: Object,
      required: true
    }
  },
  data() {
    // const requiredBlurRule = [
    //   {
    //     required: true,
    //     message: '不能为空',
    //     trigger: 'blur'
    //   }
    // ]
    const requiredChangeRule = [
      {
        required: true,
        message: '不能为空',
        trigger: 'change'
      }
    ]

    return {
      dialogVisible: true,
      saving: false,
      clueAuditOptions: CONSTANTS.CLUE_AUDIT,
      disposeStepOptions: CONSTANTS.DISPOSE_STEP,
      awardSituationOptions: CONSTANTS.AWARD_SITUATION,
      awardDone: CONSTANTS.AWARD_DONE,
      form: {
        checkStatus: CONSTANTS.INSPECT_IS,
        checkDetails: '',
        verification: '',
        disposalMeasures: '',
        rewardStatus: '',
        rewardAmount: '',
        rewardTime: ''
      },
      rules: {
        verification: requiredChangeRule,
        disposalMeasures: requiredChangeRule,
        rewardStatus: requiredChangeRule,
        rewardAmount: $rNumberValidate({required: true, num2: 2, min: 0.01}),
        rewardTime: requiredChangeRule
      }
    }
  },
  computed: {
    firstFont() {
      return this.viewData.informName && this.viewData.informName[0]
    }
  },
  watch: {},
  created() {
  },
  mounted() {},
  methods: {
    submit(){
      this.$refs.form.validate(valid => {
        if(valid){
          this.saving = true
          let form = {...this.form}
          if(this.form.rewardStatus === this.awardDone) {
            form.rewardAmount = form.rewardAmount * 100
          }else{
            form.rewardAmount = null
            form.rewardTime = null
          }
          form.checkTime = dayjs().format('YYYY/MM/DD')
          form.checkEnterpriseName = this.viewData.informName
          form.checkUnit = (this.viewData.areaId || this.$store.state.main.userInfo.areaId)
          console.log(form)

          // this.$http.put('/informs/process/' + this.viewData.id, form).then(res => {
          //   if (res.code === this.$root.codeSuccess) {
          //     this.$message.success('核查成功')
          //     this.cancel()
          //     this.$emit('reload')
          //   }
          //   this.saving = false
          // })
        }
      })
    },
    cancel() {
      this.$emit('close')
    },
    reset() {
      for(let key in this.form) {
        this.form[key] = ''
      }
    }
  },
  beforeDestroy() {},
  components: {}
}
</script>




