<template>
  <div class="map-container">
    <div class="total">
      <div>
        辖区科技企业数量: <span class="num">{{ totalNum }}</span> 个
      </div>
      <div>
        今年新增数量: <span class="num">{{ increaseNum }}</span> 个
      </div>
    </div>
    <div id="amap" class="cq-map"></div>
  </div>
</template>
<script>
  import MapLoader from '../echarts-js/amap'
  export default {
    title: '高德地图',
    data () {
      return {
        distance: 1,
        companies: [],
        totalNum: 0,
        increaseNum: 0
      }
    },
    mounted () {
      this.getTotal()
      this.scottmap()
    },
    methods: {
      getTotal () {
        this.$http.get('/statistics/companyNum', '', {customMessage: true}).then(res => {
          if (res.code === this.$root.codeSuccess) {
            this.totalNum = res.content.total
            this.increaseNum = res.content.annualGrowth
          }
        })
      },
      async scottmap() {
        let map = ''
        MapLoader().then(AMap => {
          console.log('地图加载成功')
          map = new AMap.Map('amap', {
            resizeEnable: true,
            center: [106.58, 29.60],
            zoom: 12,
            mapStyle: 'amap://styles/whitesmoke'
          })

          this.companies = []
          this.$http.get('/statistics/companyMap', '', {customMessage: true}).then(res => {
            if(res.code === this.$root.codeSuccess) {
              res.content.forEach((item) => {
                if (item.lon && item.lat) {
                  item.style = 0
                  item.lnglat = [item.lon, item.lat]
                  this.companies.push(item)
                }
              })

              var style = [{
                url: 'static/img/map/color-blue.svg',
                anchor: new AMap.Pixel(5, 5),
                size: new AMap.Size(15, 15),
                zIndex: 1
              }]
              // var citys = [{'lnglat': ['116.258446', '37.686622'], 'name': '景县', 'style': 0}]

              var mass = new AMap.MassMarks(this.companies, {
                opacity: 0.8,
                zIndex: 111,
                cursor: 'pointer',
                style: style
              })

              var marker = new AMap.Marker({content: ' ', map: map})

              mass.on('mouseover', function (e) {
                marker.setPosition(e.data.lnglat)
                marker.setLabel({content: e.data.entName})
              })

              mass.setMap(map)
            }
          })
        }, e => {
          console.log('地图加载失败', e)
        })
      }
    }
  }

</script>
<style lang="scss" scoped>
  .map-container {
    display: flex;
    flex-direction: column;
    background-color: white;
    height: 396px;
    .total {
      display: flex;
      justify-content: space-around;
      margin-bottom: 10px;
      .num {
        font-size: 20px;
        font-weight: bold;
      }
    }
    .cq-map {
      height: 350px;
      padding: 10px;
    }
  }
</style>



