<template>
  <div>
    <div class="content-view"
         v-loading="table.loading">
      <div class="table-query-container">
        <div class="table-query">
          <div class="table-query-item">
            <div class="table-query-label">企业名称:</div>
            <el-input v-model="params.key"
                      class="table-query-content"
                      clearable
                      placeholder="请输入企业名称或者信用代码" />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">辖区街道:</div>
            <el-select filterable clearable v-model="params.street" placeholder="请选择辖区街道">
              <el-option
                v-for="item in streets"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </div>
          <div class="table-query-item">
            <div class="table-query-label">经营状态:</div>
            <el-select clearable v-model="params.operatingStatus" placeholder="请选择经营状态">
              <el-option
                v-for="item in status"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>

          </div>
        </div>
        <div class="table-query-btn">
          <el-button type="primary"
                     @click="m_queryTable">查询</el-button>
          <el-button type="primary"
                     @click="m_resetTable">重置</el-button>
        </div>
      </div>
      <div class="table-container">
        <el-table :data="table.data"
                  stripe
                  border
                  @sort-change="sortChange"
                  :default-sort = "{prop: 'regDate', order: 'descending'}"
                  style="width: 100%">
          <el-table-column type="index"
                           width="50"
                           label="序号">
          </el-table-column>
          <el-table-column prop="entName"
                           label="企业名称"
                           width="220">
          </el-table-column>
          <el-table-column prop="operatingStatus"
                           label="状态"
                           width="100">
          </el-table-column>
          <el-table-column prop="street"
                           label="所属街道"
                           width="120">
          </el-table-column>
          <el-table-column prop="fr"
                           width="100"
                           label="企业法人">
          </el-table-column>
          <el-table-column prop="creditCode"
                           label="信用代码"
                           width="190">
          </el-table-column>
          <el-table-column prop="employeesNum"
                           label="企业人数"
                           sortable
                           width="120">
          </el-table-column>
          <el-table-column prop="regDate"
                           sortable="custom"
                           label="注册时间"
                            width="150">
          </el-table-column>
          <el-table-column prop="businessStabilityScore"
                           label="稳定性得分"
                           sortable
                           width="120">
          </el-table-column>
          <el-table-column prop="businessRiskScore"
                           sortable
                           label="经营风险得分"
                           width="130">
          </el-table-column>
          <el-table-column prop="legalRiskScore"
                           sortable
                           label="法律风险评分"
                           width="130">
          </el-table-column>
          <el-table-column prop="intellectualPropertyScore"
                           sortable
                           label="产权价值评分">
          </el-table-column>
          <el-table-column width="100"
                           label="查看"
                           fixed="right">
            <template slot-scope="scope">
              <el-button type="primary" plain @click="showDetail(scope.row.entName)">查看</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-pagination :current-page.sync="params.pageNo"
                     :page-size.sync="params.pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :page-sizes="pageSizes"
                     :total="table.total"
                     @size-change="m_handleSizeChange"
                     @current-change="m_handleCurrentChange" />
    </div>
    <component :is="currentOperateView"
               :viewData="viewData"
               @close="currentOperateView=''" />
  </div>
</template>
<script>
import tableQuery from '@/mixins/tableQuery'
import RiskTestView from '@/views/risk_test/RiskTestView'
export default {
  name: 'CompanyManage',
  mixins: [tableQuery],
  components: {
    RiskTestView
  },
  props: {
    menuTitle: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      params: {
        key: '',
        operatingStatus: '',
        street: '',
        orderColumn: '',
        orderingSpecification: ''
      },
      tableUrl: '/company/page',
      streets: [],
      status: [{
        value: '在营',
        label: '在营'
      }, {
        value: '吊销',
        label: '吊销'
      }, {
        value: '注销',
        label: '注销'
      }, {
        value: '其他',
        label: '其他'
      }],
      viewData: null,
      currentOperateView: ''
    }
  },
  mounted () {
    // 获取街道
    this.getStreets()
  },
  computed: {
    searchUrl() {
      return this.$root.isScience ? '/detect/science/' : '/detect/finance/'
    }
  },
  methods: {
    getStreets () {
      this.streets = []
      this.$http.get('/company/streets', '', {customMessage: true}).then(res => {
        if(res.code === this.$root.codeSuccess) {
          res.content.forEach((item) => {
            let lv = {
              label: item,
              value: item
            }
            this.streets.push(lv)
          })
        }
      })
    },
    sortChange (val) {
      this.params.orderColumn = val.prop
      this.params.orderingSpecification = val.order === 'descending' ? 'DESC' : 'ASC'
      console.log('排序查询', this.params)
      this.m_queryTable()
    },
    showDetail (entName) {
      console.log('查看公司详情', entName)
      if (entName) {
        this.table.loading = true
        this.$http.get(this.searchUrl + encodeURIComponent(entName)).then(res => {
          if(res.code === this.$root.codeSuccess) {
            if(res.content && typeof res.content === 'object') {
              this.viewData = res.content
              this.currentOperateView = 'RiskTestView'
            }else{
              this.$message.warning('未检索到该企业信息')
            }
          }
          this.table.loading = false
        })
      }else{
        this.$message.warning('请输入企业名称')
      }
    }
  }
}
</script>



