<template>
  <div class="password-show-value">
    <el-input v-model="showValue"
              :placeholder="placeholder"
              :disabled="disabled"
              :size="size" />
    <el-input class="show-value"
              v-model="_value"
              :placeholder="placeholder"
              :disabled="disabled"
              :size="size"
              @blur="(e)=>{$emit('blur',e)}" />
  </div>
</template>
<script>
export default {
  name: 'ElInputPassword',
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {
      type: [String, Number],
      default: ''
    },
    size: {
      type: String,
      default: 'small'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: '请输入'
    },
    /**
     * 组件配置
     */
    componentConfig: {
      type: String,
      default: '天'
    }
  },
  computed: {
    _value: {
      get() {
        return this.value
      },
      set(value) {
        this.$emit('change', value)
      }
    },
    showValue() {
      return (this.value || '').replace(/./g, '*')
    }
  },
  methods: {
    handleChange(value) {
      this.$emit('change', value)
    }
  }
}
</script>
<style lang="scss">
.password-show-value {
  position: relative;
  .el-input__inner {
    position: relative;
    z-index: 9;
  }
  .show-value {
    position: absolute;
    z-index: 8;
    top: 0;
    left: 0;
    .el-input__inner {
      color: transparent;
      background: transparent;
    }
  }
}
</style>






