<template>
  <div class="container-absolute border-none"
       v-loading="loading">
    <div class="overflow-auto">
      <div class="padding-20 border background-white">
        <div class="row-name-title">
          <div class="first-font">{{firstFont}}</div>
          <div class="row-title-content">
            <div class="row-title-top">
              <div class="name-title">{{viewData.informName}}</div>
              <div class="row-name-right">
                <!-- <el-tag>{{inspectStatusOptionsObject[viewData.checkStatus]}}</el-tag> -->
                <el-button type="primary"
                          @click="$emit('close')">返回</el-button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="bound-bar"></div>
      <div class="border  background-white flex-1">
        <div class="sub-title">任务详情</div>
        <div class="blur-bar-title">举报信息</div>
        <el-table :show-header="false"
                  :data="tableData1"
                  :span-method="spanMethod1"
                  class="form-view-table common-detail-table"
                  border>
          <el-table-column width="120">
            <template slot-scope="scope">{{scope.row.label1}}</template>
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope">
              {{form[scope.row.value1]}}
            </template>
          </el-table-column>
          <el-table-column width="120">
            <template slot-scope="scope">{{scope.row.label2}}</template>
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope">
              {{form[scope.row.value2]}}
            </template>
          </el-table-column>
          <el-table-column width="120">
            <template slot-scope="scope">{{scope.row.label3}}</template>
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope">
              {{form[scope.row.value3]}}
            </template>
          </el-table-column>
        </el-table>
        <div class="blur-bar-title">核查处置情况</div>
        <el-table :show-header="false"
                  :data="tableData2"
                  :span-method="spanMethod2"
                  class="form-view-table common-detail-table"
                  border>
          <el-table-column width="120">
            <template slot-scope="scope">{{scope.row.label1}}</template>
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope">
              {{form[scope.row.value1]}}
            </template>
          </el-table-column>
          <el-table-column width="120">
            <template slot-scope="scope">{{scope.row.label2}}</template>
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope">
              {{form[scope.row.value2]}}
            </template>
          </el-table-column>
          <el-table-column width="120">
            <template slot-scope="scope">{{scope.row.label3}}</template>
          </el-table-column>
          <el-table-column>
            <template slot-scope="scope">
              {{form[scope.row.value3]}}
            </template>
          </el-table-column>
        </el-table>
        <div class="blur-bar-title">核查进展</div>
        <el-table :data="checkProgress"
                  stripe
                  border>
          <el-table-column type="index"
                           width="50"
                           label="序号">
          </el-table-column>
          <el-table-column width="150"
                           label="类型">
            <template slot-scope="scope">{{showCheckProcessTypeName(scope.row.processType)}}</template>
          </el-table-column>
          <el-table-column width="180"
                           label="时间">
            <template slot-scope="scope">{{scope.row.createTime}}</template>
          </el-table-column>
          <el-table-column label="意见"
                           show-overflow-tooltip>
            <template slot-scope="scope">{{scope.row.processMessage}}</template>
          </el-table-column>
        </el-table>

      </div>
    </div>
  </div>
</template>
<script>
import * as CONSTANTS from '@/constants/common'
export default {
  name: 'ReportComplainView',
  props: {
    viewData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      loading: false,
      form: {},
      formData: [
        {
          label: '任务名称',
          value: ''
        },
        {
          label: '到期时间',
          value: ''
        },
        {
          label: '任务编号',
          value: ''
        },
        {
          label: '发起时间',
          value: ''
        },
        {
          label: '发起单位',
          value: ''
        },
        {
          label: '核查状态',
          value: ''
        }
      ],
      tableData1: [
        {
          label1: '举报来源',
          value1: 'inform_source_text',
          label2: '举报时间',
          value2: 'inform_informTime',
          label3: '被举报对象',
          value3: 'inform_informName'
        },
        {
          label1: '举报类型',
          value1: 'inform_informType_text',
          label2: '案发时间',
          value2: 'inform_crimeTime',
          label3: '案发地址',
          value3: 'inform_adress_text'
        },
        {
          label1: '举报内容',
          value1: 'inform_informContent',
          label3: '涉及金额(元)',
          value3: 'inform_involvedAmount'
        },
        {
          isImg: true,
          label1: '举报图片'
        }
      ],
      tableData2: [
        {
          label1: '核查状态',
          value1: 'inform_checkStatus_text',
          label2: '核查时间',
          value2: 'informCheck_checkTime',
          label3: '核查单位',
          value3: 'areaName'
        },
        {
          label1: '线索审核',
          value1: 'informCheck_verification_text',
          label2: '核查企业全称',
          value2: 'informCheck_checkEnterpriseName',
          label3: '核查地点',
          value3: 'informCheck_checkPlace'
        },
        {
          label1: '涉及行业',
          value1: 'informCheck_industry',
          label2: '处置措施',
          value2: 'informCheck_disposalMeasures_text'
        },
        {
          label1: '核查详情',
          value1: 'informCheck_checkDetails'
        }
      ],
      // 核查进展
      checkProgress: []
    }
  },
  computed: {
    firstFont() {
      return this.viewData.informName && this.viewData.informName[0]
    },
    inspectStatusOptionsObject() {
      return this.$optionsToObject(CONSTANTS.INSPECT_STATUS)
    },
    reportSourceOptionsObject() {
      return this.$optionsToObject(CONSTANTS.REPORT_SOURCE)
    },
    disposeStepOptionsObject() {
      return this.$optionsToObject(CONSTANTS.DISPOSE_STEP)
    }

  },
  created() {
    this.getDetailData()
    this.getCheckProgress()
  },
  mounted() {},
  methods: {
    getDetailData() {
      this.loading = true
      this.$http.get('/informs/view/' + this.viewData.id).then(res => {
        if(res.code === this.$root.codeSuccess) {
          let obj = {}
          for(let key in res.content) {
            if(typeof res.content[key] === 'object') {
              for(let k in res.content[key]) {
                obj[key + '_' + k] = res.content[key][k]
              }
            }else{
              obj[key] = res.content[key][key]
            }
          }
          // obj.inform_source_text =
          obj.inform_adress_text = (obj.inform_crimeRegion || '') + ' ' + (obj.inform_crimeAddress || '')

          obj.inform_source_text = this.reportSourceOptionsObject[obj.inform_source]
          obj.inform_checkStatus_text = this.inspectStatusOptionsObject[obj.inform_checkStatus]
          obj.informCheck_disposalMeasures_text = this.disposeStepOptionsObject[obj.informCheck_disposalMeasures]
          // this.$set(this, 'form', obj)
          this.form = obj
        }else{
          this.cancel()
        }
        this.loading = false
      })
    },
    getCheckProgress () {
      this.loading = true
      this.$http.get('/informs/process/list/' + this.viewData.id).then(res => {
        if(res.code === this.$root.codeSuccess) {
          console.log('查看核查进度，，，', res.content)
          this.checkProgress = res.content
        }else{
          this.cancel()
        }
        this.loading = false
      })
    },
    showCheckProcessTypeName (name) {
      if (name) {
        switch (name) {
          case 'DISPATCHER':
            return '下发'
          case 'RETURN':
            return '退回'
          case 'PROCESS':
            return '处理'
          case 'RECHECK':
            return '重新核查'
          case 'REVOKE':
            return '撤回'
        }
      }
    },
    spanMethod1({rowIndex, columnIndex}) {
      if(rowIndex === 2) {
        if(columnIndex === 1) {
          return {
            rowspan: 1,
            colspan: 3
          }
        }else if(columnIndex > 1 && columnIndex < 4) {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }else if(rowIndex === 3) {
        if(columnIndex === 1) {
          return {
            rowspan: 1,
            colspan: 5
          }
        }else if(columnIndex > 1) {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }
    },
    spanMethod2({rowIndex, columnIndex}) {
      if(rowIndex === 2) {
        if(columnIndex === 3) {
          return {
            rowspan: 1,
            colspan: 3
          }
        }else if(columnIndex > 3) {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }else if(rowIndex === 3) {
        if(columnIndex === 1) {
          return {
            rowspan: 1,
            colspan: 5
          }
        }else if(columnIndex > 1) {
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }
    },
    cancel() {
      this.$emit('close')
    }
  },
  beforeDestroy() {},
  components: {}
}
</script>
<style lang="scss" scoped>
.overflow-auto {
  min-height: 100%;
}
</style>




