<template>
  <div v-loading="loading"
       class="container main-container">
    <div class="top-bar" :style="mainTopbar">
      <img class="logo"
           :src="logoSrc" />
      <!-- <el-popconfirm title="确定退出系统吗？"
                     @confirm="loginOut">
        <div class="login-out"
             slot="reference">
          <img class="user"
               src="static/img/user.png" />
          <div>{{userName}}</div>
        </div>
      </el-popconfirm> -->

      <div class="main-right-top">
        <div class="main-badge">

          <el-popover width="150"
                      trigger="click"
                      >
            <div style="color: red">
              你有<br/>
              <span>{{taskToCheckNum}} 条核查任务</span><br/>
              <span>{{informToCheckNum}} 条投诉任务</span><br/>
              需要处理
            </div>

            <el-badge :value="checkNum" class="item" slot="reference">
              <el-button size="mini">待办</el-button>
            </el-badge>
          </el-popover>


        </div>


        <el-popover v-model="showTools"
                    placement="right"
                    width="100"
                    trigger="click">
          <div class="main-user-tools">
            <el-button @click="editPassword"
                       type="text">修改密码</el-button>
            <el-popconfirm title="确定退出系统吗？"
                           @confirm="loginOut"
                           @cancel="showTools=false">
              <el-button slot="reference"
                         type="text">退出</el-button>
            </el-popconfirm>
          </div>
          <div class="login-out"
               slot="reference">
            <img class="user"
                 src="static/img/user.svg" />
            <div>{{userName}}</div>
          </div>
        </el-popover>

      </div>

    </div>
    <div class="main-content">
      <el-menu v-show="!loading"
               :default-active="activeRouter"
               active-text-color="#0082FF">
        <el-menu-item v-for="menu in showMenus"
                      :key="menu.name"
                      :index="menu.name"
                      @click.native="clickMenu(menu)">
          <img :src="`static/img/${menu.icon+(activeRouter===menu.name?'_active':'')}.${menu.isSvg?'svg':'png'}`"/>
          <span slot="title">{{menu.title}}</span>
        </el-menu-item>
      </el-menu>
      <div class="view-content">
        <el-tabs v-model="activeRouter"
                 type="card"
                 closable
                 @tab-click="clickTab"
                 @tab-remove="removeTab">
          <el-tab-pane v-for="item in routerTabs"
                       :key="item.name"
                       :label="item.title"
                       :name="item.name">
          </el-tab-pane>
        </el-tabs>
        <router-view class="current-router-view"
                     :menuTitle="activeRouterTitle" />
      </div>
    </div>

    <el-dialog title="修改密码"
               width="400px"
               :visible.sync="dialogVisible"
               :before-close="cancel">
      <el-form :model="form"
               :rules="rules"
               ref="form">
        <el-form-item prop="oldPassword">
          <el-input v-model="form.oldPassword"
                    type="password"
                    auto-complete="new-password"
                    @dblclick.native.prevent
                    @mousedown.native="(event)=>{event.target.setAttribute('type','text')}"
                    @focus="(event)=>{event.target.setAttribute('type','password')}"
                    @blur="(event)=>{event.target.setAttribute('type','password')}"
                    placeholder="原密码" />
        </el-form-item>
        <el-form-item prop="password">
          <el-input v-model="form.password"
                    type="password"
                    auto-complete="new-password"
                    @dblclick.native.prevent
                    @mousedown.native="(event)=>{event.target.setAttribute('type','text')}"
                    @focus="(event)=>{event.target.setAttribute('type','password')}"
                    @blur="(event)=>{event.target.setAttribute('type','password')}"
                    placeholder="新密码" />
        </el-form-item>
        <el-form-item prop="confrimPassword">
          <el-input v-model="form.confrimPassword"
                    type="password"
                    auto-complete="new-password"
                    @dblclick.native.prevent
                    @mousedown.native="(event)=>{event.target.setAttribute('type','text')}"
                    @focus="(event)=>{event.target.setAttribute('type','password')}"
                    @blur="(event)=>{event.target.setAttribute('type','password')}"
                    placeholder="新密码确认" />
        </el-form-item>
      </el-form>
      <div slot="footer">
        <el-button type="primary"
                   @click="submit"
                   :loading="saving">确定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { MAIN_USER_INFO_GET, MAIN_USER_MENU_GET } from '@/store/types/main'
export default {
  name: 'Main',
  mixins: [],
  props: {},
  data() {
    const that = this
    const requiredBlurRule = [
      {
        required: true,
        message: '不能为空',
        trigger: 'blur'
      }
    ]
    const passwordRule = [
      {
        required: true,
        message: '不能为空',
        trigger: 'blur'
      },
      {
        pattern: /^\w{8,16}$/,
        message: '密码必须为8-16为数字、字母或下划线',
        trigger: 'blur'
      }
    ]
    return {
      menus: [
        {
          icon: 'home_page',
          isSvg: true,
          name: 'HomePage',
          title: '首页',
          menuName: 'risk.statistics'
        },
        {
          icon: 'm4',
          name: 'Statistics',
          title: '统计查询',
          menuName: 'report'
        },
        {
          icon: 'm2',
          name: 'CheckDispose',
          title: '核查处理',
          menuName: 'verify.management'
        },
        {
          icon: 'risk_test',
          isSvg: true,
          name: 'RiskTest',
          title: '风险检测',
          menuName: 'risk.management'
        },
        {
          icon: 'm3',
          name: 'ReportComplain',
          title: '举报投诉',
          menuName: 'inform.management'
        },
        {
          icon: 'user_manage',
          isSvg: true,
          name: 'UserManage',
          title: '用户管理',
          menuName: 'user.management'
        },
        {
          icon: 'company',
          isSvg: true,
          name: 'CompanyManage',
          title: '企业管理',
          menuName: 'company.management'
        }
      ],
      routerTabs: [],
      activeRouter: '',
      showTools: false,
      loading: false,

      checkNum: 0,
      informToCheckNum: 0,
      taskToCheckNum: 0,

      dialogVisible: false,
      saving: false,
      form: {
        oldPassword: '',
        password: '',
        confirmPassword: ''
      },
      rules: {
        oldPassword: requiredBlurRule,
        password: passwordRule,
        confrimPassword: [
          // ...passwordRule,
          {
            validator: (rule, value, callback) => {
              if(value === that.form.password) {
                callback()
              }else{
                callback(new Error('两次密码必须一致'))
              }
            },
            trigger: 'blur'
          }
        ]
      }
    }
  },
  computed: {
    activeRouterTitle() {
      let route = this.menus.find(m => m.name === this.activeRouter)
      return route && route.title
    },
    userName() {
      return this.$store.state.main.userInfo.userName
    },
    showMenus() {
      // return this.menus
      let menus = this.$store.state.main.userMenu
      let res = this.menus.filter(m => menus.some(_m => _m.menuName === m.menuName))
      console.log('所获取的菜单信息', res)
      return res
    },
    logoSrc() {
      return this.$root.isScience ? 'static/img/logo-1.png' : 'static/img/logo-2.png'
    },
    mainTopbar () {
      return 'background-image: url(static/img/mainTopbar.png);'
    }
  },
  watch: {},
  created() {
    if(this.$route.path.indexOf('main/') > -1) {
      console.log('path', this.$route.path)
      let menu = this.menus.find(m => m.name === this.$route.name)
      if(menu) {
        this.routerTabs.push({...menu})
        this.activeRouter = menu.name
      }
    }
    this.initSystem()
    this.getCheckTask()
  },
  methods: {
    initSystem() {
      this.loading = true
      let p1 = this.$store.dispatch(MAIN_USER_INFO_GET)
      let p2 = this.$store.dispatch(MAIN_USER_MENU_GET)
      Promise.all([p1, p2]).then(res => {
        // if(res.every(r => r.code === this.$root.codeSuccess)) {
        //   this.loading = false
        // }
        this.loading = false
      })
    },
    getCheckTask () {
      this.$http.get('/report/pendinglist', '', {customMessage: true}).then(res => {
        if(res.code === this.$root.codeSuccess) {
          if (res.content) {
            this.informToCheckNum = res.content.informToCheckNum
            this.taskToCheckNum = res.content.taskToCheckNum
            this.checkNum = this.informToCheckNum + this.taskToCheckNum
          }
        }
      }, e => {
        console.log('获取待办任务事项失败', e)
      })
    },
    loginOut() {
      this.showTools = false
      // this.$confirm('确定退出系统吗？', '提示').then(() => {
      this.$router.push({
        name: 'Login'
      })
      // })
    },
    editPassword() {
      this.showTools = false
      this.dialogVisible = true
    },
    clickMenu(menu) {
      if(this.$route.name !== menu.name) {
        this.$router.push({name: menu.name})
      }

      this.activeRouter = menu.name
    },
    clickTab(tab) {
      this.$router.push({name: tab.name})
    },
    addTab(targetName) {
      let newTabName = ++this.tabIndex + ''
      this.routerTabs.push({
      })
      this.activeRouter = newTabName
    },
    removeTab(targetName) {
      let tabs = this.routerTabs
      let activeName = this.activeRouter
      if (activeName === targetName) {
        let nextTab = this.routerTabs.findIndex(t => t.name === targetName)
        if(nextTab > -1) {
          nextTab = this.routerTabs[nextTab + 1] || this.routerTabs[nextTab - 1]
          if(nextTab) {
            activeName = nextTab.name
          }else{
            activeName = ''
          }
        }
      }
      if(activeName && this.activeRouter !== activeName) {
        this.$router.push({name: activeName})
      }
      this.activeRouter = activeName
      this.routerTabs = tabs.filter(tab => tab.name !== targetName)
      if(!activeName) {
        this.$router.replace({name: 'Main'})
      }
    },
    submit() {
      this.$refs.form.validate(valid => {
        if(valid) {
          this.saving = true
          let form = {...this.form}
          delete form.confirmPassword
          this.$http.put('/users/password', form).then(res => {
            this.saving = false
            if(res.code === this.$root.codeSuccess) {
              this.$confirm('密码修改成功!请重新登陆', '提示', {
                showCancelButton: false,
                beforeClose: (action, instance, done) => {
                  done()
                  this.cancel()
                  this.$router.replace({name: 'Login'})
                }
              })
            }
          })
        }
      })
    },
    cancel() {
      this.dialogVisible = false
    }
  },
  beforeRouteUpdate (to, from, next) {
    if(to.name !== 'Main' && !this.routerTabs.some(t => t.name === to.name)) {
      let menu = this.menus.find(m => to.name === m.name)
      if(menu) {
        this.routerTabs.push({...menu})
      }
    }
    if(this.activeRouter !== to.name) {
      this.activeRouter = to.name
    }
    next()
  },
  components: {}
}
</script>



