// 取值类型校验
export default {
  data() {
    return {
      keyword: '',
      inputTimer: null
    }
  },
  computed: {
    optionTipText() {
      if(this.multiple && this.multipleLimit) {
        return `最多选择${this.multipleLimit}个`
      }else{
        return ''
      }
    },
    optionTipShow() {
      return this.multiple && this.multipleLimit && this.multipleLimit === this._value.length
    }
  },
  methods: {
    mESfilterMethod(keyword) {
      this.keyword = keyword
    },
    mESinput() {
      if(!this.multiple) {
        clearTimeout(this.inputTimer)
        this.inputTimer = setTimeout(() => {
          let $data = this.$refs.select.$data
          if(this.keyword !== $data.selectedLabel && $data.query !== $data.selectedLabel) {
            this.keyword = $data.selectedLabel
          }
        }, 100)
      }
    },
    mESvisibleChange(visible) {
      let event = visible ? 'focus' : 'blur'
      this.$emit(event)
      this.$emit('visibleChange', visible)
      // 隐藏时清空检索关键字
      if(!visible) {
        setTimeout(() => {
          this.keyword = ''
        }, 300)
      }
    }
  }
}



