<template>
  <div class="increase-container">
    <div id="historyIncrease" class="echarts-height"></div>
  </div>
</template>
<script>
  import * as echarts from 'echarts'
  export default {
    title: '历年新增企业数量',
    data () {
      return {
        xData: [],
        yDataLine: [],
        yDataBar: []
      }
    },
    mounted () {
      this.initEcharts()
    },
    methods: {
      async initEcharts () {
        let mychart = echarts.init(document.getElementById('historyIncrease'))
        this.$http.get('/statistics/addedOverTheYears', '', {customMessage: true}).then(res => {
          if (res.code === this.$root.codeSuccess) {
            res.content.forEach((item) => {
              this.xData.push(item.yearName === -1 ? '之前' : item.yearName + '')
              this.yDataBar.push(item.incrementNum)
              this.yDataLine.push(item.incrementNum)
            })

            let option = {
              title: {
                text: '历年新增企业数量',
                left: 'left',
                top: 10,
                textStyle: {
                  color: '#1E90FF'
                }
              },
              tooltip: {
                trigger: 'axis',
                axisPointer: {
                  type: 'shadow'
                }
              },
              xAxis: {
                type: 'category',
                data: this.xData.reverse()
              },
              yAxis: {
                type: 'value'
              },
              series: [{
                data: this.yDataBar.reverse(),
                type: 'bar'
              }, {
                data: this.yDataLine.reverse(),
                type: 'line'
              }]
            }
            mychart.setOption(option)
          }
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  .increase-container {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
    background-color: white;
    height: 350px;
    .echarts-height {
      height: 300px;
    }
  }
</style>



