<template>
  <div class="container login-container">
    <div class="login-box">
      <img class="logo"
           :src="logoSrc" />
      <el-form class="login-form"
               :model="form"
               :rules="rules"
               ref="form">
        <el-form-item prop="loginName">
          <el-input placeholder="用户名"
                    prefix-icon="el-icon-user"
                    v-model="form.loginName">
          </el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input placeholder="密码"
                    prefix-icon="el-icon-lock"
                    type="password"
                    v-model="form.password">
          </el-input>
        </el-form-item>
        <el-button type="primary"
                   class="width-100"
                   @click="login"
                   :loading="loading">登陆</el-button>
      </el-form>
    </div>
  </div>
</template>
<script>
// import { MAIN_USER_INFO_GET } from '@/store/types/main'
export default {
  name: 'Login',
  mixins: [],
  props: {},
  data() {
    const requiredBlurRule = [
      {
        required: true,
        message: '不能为空',
        trigger: 'blur'
      }
    ]
    return {
      loading: false,
      form: {
        loginName: '',
        password: ''
      },
      rules: {
        loginName: requiredBlurRule,
        password: requiredBlurRule
      }
    }
  },
  computed: {
    logoSrc() {
      return this.$root.isScience ? 'static/img/login-bg-2-1.png' : 'static/img/login-bg-2-2.png'
    }
  },
  watch: {},
  created() {},
  mounted() {},
  methods: {
    login() {
      // this.$router.push('/main')
      this.$refs.form.validate(valid => {
        if(valid) {
          this.loading = true
          this.$http.post('/login', this.form).then(res => {
            if(res.code === this.$root.codeSuccess) {
          // this.$store.dispatch(MAIN_USER_INFO_GET).then(res => {
          //   if(res.code === this.$root.codeSuccess) {
          //     this.$router.push('/main')
          //   }
          // })
              this.$router.push('/main')
            }
            this.loading = false
          })
        }
      })
    }
  },
  beforeDestroy() {},
  components: {}
}
</script>




