    spanMethod1({rowIndex, columnIndex}) {
      if(columnIndex === 0) {
        if(rowIndex === 2) {
          return {
            rowspan: 4,
            colspan: 1
          }
        }else if(rowIndex > 2){
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }else if(rowIndex < 2) {
        if(columnIndex % 2) {
          return {
            rowspan: 1,
            colspan: 2
          }
        }else{
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }
    },
    spanMethod2({rowIndex, columnIndex}) {
      if(columnIndex < 2) {
        if(rowIndex === 0 || rowIndex === 3){
          return {
            rowspan: 3,
            colspan: 1
          }
        }else{
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }
    },
    spanMethod3({rowIndex, columnIndex}) {
      if(columnIndex < 4) {
        if(rowIndex === 0){
          return {
            rowspan: this.tData4.length,
            colspan: 1
          }
        }else{
          return {
            rowspan: 0,
            colspan: 0
          }
        }
      }
    },
    cellClassName1({rowIndex, columnIndex}) {
      if(rowIndex === 0) {
        return 'label-table-cell'
      }else if(rowIndex === 1){
        if(columnIndex === 0) {
          return 'label-table-cell'
        }
      }else {
        if(columnIndex === 0 || columnIndex % 2) {
          return 'label-table-cell'
        }
      }
    },
    cellClassName2({rowIndex, columnIndex}) {
      if(rowIndex === 0 && columnIndex === 5) {
        return 'label-table-cell'
      }
    },
    fastPostionChange (node) {
      console.log('选择的内容、、', node)
      // 当前窗口正中心位置到指定dom位置的距离
      // 页面滚动了的距离
      const height = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      // 获取容器元素
      const container = document.getElementById('risk-test-content')
      // 指定dom到页面顶端的距离
      const dom = document.getElementById(node.value)
      const domHeight = dom.offsetTop - 50
      // 滚动距离计算
      var S = Number(height) - Number(domHeight)
      // 判断上滚还是下滚
      if (S < 0) {
        // 下滚
        S = Math.abs(S)
        container.scrollTop = S
      } else {
        // 上滚
        S = -S
        container.scrollTop = S
      }
    }
  },
  components: {
    GaugeChart
