/**
 * 引入自定义组织然后全局注册
 */
import Vue from 'vue'
// import MenuSelectTree from '../components/tree/MenuSelectTree.vue'
import ElSelectMine from '../components/element_mine/ElSelectMine.vue'
import ElInputPassword from '../components/element_mine/ElInputPassword.vue'
import ElDateTimeRangeTwoValuePicker from '../components/element_mine/ElDateTimeRangeTwoValuePicker.vue'
import TipText from '../components/common/TipText.vue'
// 全局公共组件
let comArr = [
  ElSelectMine,
  ElInputPassword,
  ElDateTimeRangeTwoValuePicker,
  TipText
]
// 自定义特殊业务组件

let customArr = []

let _comArr = comArr.concat(customArr)
_comArr.forEach(com => {
  if (!com.name) {
    return console.error('组件必须要有name')
  }
  // Vue.use({
  //   install(Vue, component) {
  //     Vue.component(component.name, component)
  //   }
  // }, com)
  Vue.component(com.name, com)
})



