<template>
  <div>
    <div class="current-router-title">{{menuTitle}}
      <div v-if="$root.isAreaUser"
           class="current-router-operate">
        <el-button type="primary"
                   icon="el-icon-user"
                   @click="allot">一键分配</el-button>
        <el-button icon="el-icon-bottom-left"
                   @click="importFile">导入</el-button>
        <el-button icon="el-icon-top-right"
                   @click="exportFile">导出</el-button>
        <el-button icon="el-icon-download"
                   @click="downloadTemplate">下载模版</el-button>
      </div>
    </div>
    <div class="content-view"
         v-loading="table.loading">
      <div class="table-query-container">
        <div class="table-query">
          <div class="table-query-item">
            <div class="table-query-label">处置阶段:</div>
            <el-select-mine v-model="params.disposalStage"
                            :options="disposeStageOptions"
                            class="table-query-content"
                            clearable />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">发起时间:</div>
            <el-date-time-range-two-value-picker :value1="params.taskTimeStart"
                                                 :value2="params.taskTimeEnd"
                                                 @changeValue="(v)=>{params.taskTimeStart=v[0],params.taskTimeEnd=v[1]}"
                                                 class="el-date-two-value-picker table-query-content"
                                                 clearable />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">核查企业名称:</div>
            <el-input v-model="params.enterpriseName"
                      class="table-query-content"
                      placeholder="核查企业名称"
                      clearable />
          </div>
          <!-- <div v-if="$root.isStreetUser"
               class="table-query-item">
            <div class="table-query-label">任务名称:</div>
            <el-input v-model="params.enterpriseName"
                      class="table-query-content"
                      placeholder="任务名称"
                      clearable />
          </div> -->
          <div class="table-query-item">
            <div class="table-query-label">逾期:</div>
            <el-select-mine v-model="params.overdue"
                            :options="overdueOptions"
                            class="table-query-content"
                            clearable />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">到期时间:</div>
            <el-date-time-range-two-value-picker :value1="params.taskExpireStart"
                                                 :value2="params.taskExpireEnd"
                                                 @changeValue="(v)=>{params.taskExpireStart=v[0];params.taskExpireEnd=v[1]}"
                                                 class="el-date-two-value-picker table-query-content"
                                                 clearable />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">核查状态:</div>
            <el-select-mine v-model="params.checkStatus"
                            :options="inspectStatusOptions"
                            class="table-query-content"
                            clearable />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">核查地区</div>
            <el-cascader v-model="params.areaId"
                         :props="areaProps"
                         class="table-query-content"
                         filterable
                         clearable />
          </div>
          <div v-if="$root.isAreaUser"
               class="table-query-item">
            <div class="table-query-label">分配状态:</div>
            <el-select-mine v-model="params.assignment"
                            :options="allotStatusOptions"
                            class="table-query-content"
                            clearable />
          </div>
        </div>
        <div class="table-query-btn">
          <el-button type="primary"
                     @click="m_queryTable">查询</el-button>
          <el-button type="primary"
                     @click="m_resetTable">重置</el-button>
        </div>
      </div>
      <div class="table-container">
        <div>
          <el-table :data="table.data"
                    stripe
                    border
                    style="width: 100%"
                    :row-key="selectOperate.majorKey"
                    @select="m_selectChange"
                    @select-all="m_selectAll"
                    @row-click="m_rowClick">
            <el-table-column v-if="$root.isAreaUser"
                             type="selection"
                             width="40"
                             fixed="left">
            </el-table-column>
            <el-table-column type="index"
                             width="50"
                             label="序号"
                             fixed="left">
            </el-table-column>
            <el-table-column prop="taskNumber"
                             label="任务编号"
                             width="150"
                             show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="enterpriseName"
                             label="核查企业名称"
                             min-width="150"
                             show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="checkRegion"
                             label="核查地区"
                             show-overflow-tooltip>
            </el-table-column>
            <el-table-column prop="relatedClues"
                             label="相关线索"
                             show-overflow-tooltip>
            </el-table-column>
            <el-table-column label="处置阶段"
                             width="100">
              <template slot-scope="scope">{{disposeStageOptionsObject[scope.row.disposalStage]}}</template>
            </el-table-column>
            <el-table-column v-if="$root.isAreaUser"
                             label="分配状态"
                             width="100">
              <template slot-scope="scope">{{allotStatusOptionsObject[scope.row.assignment]}}</template>
            </el-table-column>

            <el-table-column label="核查街道"
                             prop="areaName"
                             width="100">
            </el-table-column>

            <el-table-column label="核查联系方式"
                             width="180"
                             show-overflow-tooltip>
              <template slot-scope="scope">
                <div v-if="scope.row.areaContact">
                  <div v-for="item in scope.row.areaContact">
                    <span class="areaContact">
                    {{`${item.userName} : ${item.phone}`}}
                    </span><br/>
                  </div>

                </div>
              </template>
            </el-table-column>

            <el-table-column label="核查状态"
                             width="100">
              <template slot-scope="scope">{{inspectStatusOptionsObject[scope.row.checkStatus]}}</template>
            </el-table-column>
            <el-table-column prop="startTime"
                             label="发起时间"
                             width="180">
            </el-table-column>
            <el-table-column prop="dueTime"
                             label="到期时间"
                             width="180">
            </el-table-column>
            <el-table-column label="逾期"
                             width="100">
              <template slot-scope="scope">{{overdueOptionsObject[scope.row.overdue]}}</template>
            </el-table-column>
            <el-table-column width="170"
                             label="操作"
                             fixed="right">
              <template slot-scope="scope">
                <el-button type="text"
                           @click="viewRow(scope.row)"
                           title="查看"
                           class="icon_button"><i class="el-icon-view"></i></el-button>
                <template v-if="$root.isAreaUser">
                  <template v-if="scope.row.assignment===allotAssigned">
                    <el-button v-if="scope.row.checkStatus===inspectIs"
                               type="text"
                               @click="reInspectRow(scope.row)"
                               title="重新核查"
                               class="icon_button"><i class="el-icon-search"></i></el-button>
                    <el-button v-else
                               type="text"
                               @click="cancelRow(scope.row)"
                               title="撤回"
                               class="icon_button"><i class="el-icon-refresh-left"></i></el-button>
                  </template>
                  <template v-else>
                    <el-button type="text"
                               @click="allotRow(scope.row)"
                               title="分派"
                               class="icon_button"><i class="el-icon-s-custom"></i></el-button>
                    <el-button type="text"
                               @click="deleteRow(scope.row)"
                               title="删除"
                               class="icon_button"><i class="el-icon-delete"></i></el-button>
                  </template>
                  <el-button v-if="scope.row.refundCount>0"
                             type="text"
                             @click="viewReturnRecord(scope.row)"
                             title="查看退回记录"
                             class="icon_button"><i class="el-icon-document"></i></el-button>
                </template>
                <template v-else>
                  <el-button v-if="scope.row.checkStatus!==inspectIs"
                             type="text"
                             @click="returnRow(scope.row)"
                             title="退回"
                             class="icon_button"><i class="el-icon-refresh-left"></i></el-button>
                  <el-button type="text"
                             @click="inspectRow(scope.row)"
                             title="核查"
                             class="icon_button"><i class="el-icon-search"></i></el-button>
                </template>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
      <el-pagination :current-page.sync="params.pageNo"
                     :page-size.sync="params.pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :page-sizes="pageSizes"
                     :total="table.total"
                     @size-change="m_handleSizeChange"
                     @current-change="m_handleCurrentChange" />
    </div>
    <component :is="currentOperateView"
               :viewData="viewData"
               @close="currentOperateView=''"
               @reload="m_tableLoadData" />
  </div>
</template>
<script>
import tableQuery from '@/mixins/tableQuery'
import tableSelect from '@/mixins/tableSelect'
import CheckDisposeImport from './CheckDisposeImport'
import CheckDisposeAllot from './CheckDisposeAllot'
import CheckDisposeInspect from './CheckDisposeInspect'
import CheckDisposeView from './CheckDisposeView'
import CheckDisposeReturnRecord from './CheckDisposeReturnRecord'
import * as CONSTANTS from '@/constants/common'
export default {
  name: 'CheckDispose',
  mixins: [tableQuery, tableSelect],
  props: {
    menuTitle: {
      type: String,
      required: true
    }
  },
  data() {
    let that = this
    return {
      params: {
        disposalStage: '',

        taskTimeStart: '',
        taskTimeEnd: '',

        taskExpireStart: '',
        taskExpireEnd: '',

        enterpriseName: '',
        overdue: '',
        areaId: '',
        assignment: ''
      },
      tableUrl: '/verifies/list',
      viewData: null,

      currentOperateView: '',

      areaProps: {
        checkStrictly: true,
        emitPath: false,
        lazy: true,
        label: 'areaName',
        value: 'id',
        lazyLoad (node, resolve) {
          let url = node.root ? '' : ('/' + node.value)
          if(node.isLeaf) {
            resolve([])
          }else{
            that.$http.get('/areas/subs' + url, '', {customMessage: true}).then(res => {
              if(res.code === that.$root.codeSuccess) {
                res.content.forEach(a => {
                  a.leaf = a.type === that.$root.areaLeaf
                })
                resolve(res.content)
              }else{
                resolve([])
              }
            })
          }
        }
      },

      disposeStageOptions: CONSTANTS.DISPOSE_STAGE,
      allotStatusOptions: CONSTANTS.ALLOT_STATUS,
      inspectStatusOptions: CONSTANTS.INSPECT_STATUS,
      overdueOptions: CONSTANTS.OVERDUE,
      awardSituationOptions: CONSTANTS.AWARD_SITUATION,

      allotAssigned: CONSTANTS.ALLOT_ASSIGNED,
      inspectIs: CONSTANTS.INSPECT_IS
    }
  },
  computed: {
    disposeStageOptionsObject() {
      return this.$optionsToObject(this.disposeStageOptions)
    },
    allotStatusOptionsObject() {
      return this.$optionsToObject(this.allotStatusOptions)
    },
    inspectStatusOptionsObject() {
      return this.$optionsToObject(this.inspectStatusOptions)
    },
    overdueOptionsObject() {
      return this.$optionsToObject(this.overdueOptions)
    },
    awardSituationOptionsObject() {
      return this.$optionsToObject(this.awardSituationOptions)
    },
    returnRowTitle() {
      return this.$root.isAreaUser ? '撤回' : '退回'
    }
  },
  methods: {
    allot() {
      let rows = this.selectOperate.currentCheckedRows.filter(row => row.assignment !== CONSTANTS.ALLOT_ASSIGNED)
      let len = rows.length
      if(len) {
        let h = this.$createElement
        let t = 20 * len
        let t1 = Math.floor(t / 60)
        let t2 = t % 60
        let customContent = [
          h('div', {}, [
            h('div', {
              class: 'text-center'
            }, '您已选择' + rows.length + '条可分配任务，任务将按照核查企业的经营地址分配给各个街道'),
            h('div', {
              class: 'text-center',
              style: {
                marginTop: '10px'
              }
            }, [
              h('span', {}, '预计耗时 : '),
              h('span', {
                style: {
                  color: '#0082FF'
                }
              }, `${t1}分${t2}秒`),
              h('span', {}, ' , 请耐心等待')
            ])
          ])
        ]
        this.$confirmTags({
          title: '分配任务',
          showCancelButton: false,
          confirmButtonText: '确认分配',
          customContent,
          beforeClose: (action, instance, done) => {
            if(action === 'confirm') {
              instance.$refs.confirm.loading = true
              this.$http.put('/verifies/dispatcher/batch', rows.map(r => r.id)).then(res => {
                instance.$refs.confirm.loading = false
                if(res.code === this.$root.codeSuccess) {
                  done()
                  let failNum = res.content.length
                  this.$message.success(`共分配${len}条任务，成功 ${len - failNum} 条， 失败 ${failNum} 条`)
                  this.m_tableLoadData()
                }
              })
            }else{
              done()
            }
          }
        })
      }else{
        this.$message({
          showClose: true,
          message: '请勾选可分配的数据',
          type: 'error'
        })
      }
    },
    importFile(){
      this.currentOperateView = 'CheckDisposeImport'
    },
    exportFile() {
      this.$downloadFileByIframe('/verifies/export' + this.$paramsSerializer(this.params))
    },
    downloadTemplate() {
      this.$downloadFileByIframe('static/template/task_template.xlsx', '')
    },
    viewRow(row) {
      this.currentOperateView = 'CheckDisposeView'
      this.viewData = row
    },
    inspectRow(row) {
      this.currentOperateView = 'CheckDisposeInspect'
      this.viewData = row
    },
    reInspectRow(row) {
      this.$http.put('/verifies/recheck/' + row.id).then(res => {
        if(res.code === this.$root.codeSuccess) {
          this.$message.success('重新核查成功')
          this.m_tableLoadData()
        }
      })
    },
    cancelRow(row) {
      this.$confirm('任务已分配，确定要撤回吗？撤回后可重新分派', '撤回任务', {
        beforeClose: (action, instance, done) => {
          if(action === 'confirm') {
            instance.$refs.confirm.loading = true
            this.$http.put('/verifies/revoke/' + row.id, '').then(res => {
              instance.$refs.confirm.loading = false
              if(res.code === this.$root.codeSuccess) {
                done()
                this.$message.success('撤回成功')
                this.m_tableLoadData()
              }
            })
          }else{
            done()
          }
        }
      })
    },
    returnRow(row) {
      this.$prompt('请输入退回原因', '退回', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /[^\s]/,
        inputErrorMessage: '请输入退回原因',
        beforeClose: (action, instance, done) => {
          if(action === 'confirm') {
            instance.$refs.confirm.loading = true
            this.$http.put('/verifies/return/' + row.id, {refundReason: instance.inputValue}).then(res => {
              instance.$refs.confirm.loading = false
              if(res.code === this.$root.codeSuccess) {
                done()
                this.$message.success('退回成功')
                this.m_tableLoadData()
              }
            })
          }else{
            done()
          }
        }
      })
    },
    allotRow(row){
      this.viewData = row
      this.currentOperateView = 'CheckDisposeAllot'
    },
    deleteRow(row){
      this.$confirm('确认删除吗？', '删除 ').then(() => {
        this.$http.delete('/verifies/del/' + row.id, '').then(res => {
          if(res.code === this.$root.codeSuccess) {
            this.$message.success('删除成功')
            this.m_tableLoadData()
          }
        })
      })
    },
    viewReturnRecord(row) {
      this.viewData = row
      this.currentOperateView = 'CheckDisposeReturnRecord'
    }
  },
  beforeDestroy() {},
  components: {
    CheckDisposeImport,
    CheckDisposeAllot,
    CheckDisposeInspect,
    CheckDisposeView,
    CheckDisposeReturnRecord
  }
}
</script>
<style lang="scss" scoped>
  .areaContact {
    white-space: nowrap;
  }
</style>



