<template>
  <el-select v-model="_value"
             :key="options.length"
             :filterable="filterable"
             :clearable="clearable"
             :disabled="disabled"
             :multiple="multiple"
             :multipleLimit="multipleLimit"
             :filter-method="mESfilterMethod"
             :placeholder="placeholder"
             :collapseTags="collapseTags"
             :allow-create="allowCreate"
             :default-first-option="defaultFirstOption"
             @clear="clearSelect"
             @visible-change="mESvisibleChange"
             @input.native="mESinput"
             ref="select"
             :class="{
               'is-disabled':disabled
             }"
             :size="size">
    <template v-for="(o,index) in options">
      <el-option v-if="optionShow(o)"
                 :key="index+'_'+keyword"
                 :value="textOptionFlag?o:o[_optionsKey.value]"
                 :label="textOptionFlag?o:o[_optionsKey.label]"
                 :disabled="o.disabled">
        <template>
          <template>
            <i v-if="o.icon"
               class="iconfont"
               :class="o.dataSource?'field-fn-icon':'field-value-icon'"
               v-html="o.icon"></i>
          </template>
          <template v-if="leftRightOptionsKey">
            <span class="left">{{o[leftRightOptionsKey.left]}}</span>
            <span class="right">{{o[leftRightOptionsKey.right]}}</span>
          </template>
          <template v-else>{{textOptionFlag?o:o[_optionsKey.label]}}</template>
          <span v-if="optionTipShow"
                class="option-tip">{{optionTipText}}</span>
        </template>
      </el-option>
    </template>
  </el-select>
  <!-- </el-select-new> -->
</template>
<script>
// todo ElSelectNew(目前仅在 分组指定共享里用到) 是在 原有基础上更改支持 单个标签禁用
// 若系统其他下拉框有问题可能对 分组 指定共享下拉框 需要单独封组件
// import ElSelectNew from './ElSelectNew.vue'
import elSelect from '../../mixins/elSelect.js'
export default {
  mixins: [elSelect],
  name: 'ElSelectMine',
  model: {
    prop: 'value',
    event: 'change'
  },
  props: {
    value: {},
    multiple: {
      type: Boolean,
      default: false
    },
    multipleLimit: {
      type: Number,
      default: 0
    },
    options: {
      type: Array,
      default: () => []
    },
    optionsKey: {
      type: Object,
      default: () => ({})
    },
    filterable: {
      type: Boolean,
      default: true
    },
    clearable: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    size: {
      type: String,
      default: 'small'
    },
    placeholder: {
      type: String,
      default: '请选择'
    },
    collapseTags: {
      type: Boolean,
      default: false
    },
    allowCreate: {
      type: Boolean,
      default: false
    },
    defaultFirstOption: {
      type: Boolean,
      default: false
    },
    leftRightOptionsKey: {
      type: Object,
      default: null
    },
    /**
     * 选项为纯文本即label和value相同
     */
    textOption: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    _optionsKey() {
      return Object.assign({
        label: 'label',
        value: 'value'
      }, this.optionsKey)
    },
    _value: {
      get() {
        return this.value
      },
      set(value) {
        this.$emit('change', value)
      }
    },
    textOptionFlag() {
      return this.textOption || this.allowCreate
    }
  },
  created() {
    // 设置默认值为[]
    if(this.multiple) {
      if(!(this.value instanceof Array)) {
        this.$emit('change', [])
      }
    }else if(this.value === undefined){
      this.$emit('change', '')
    }
  },
  methods: {
    optionShow(option) {
      if(this.keyword) {
        if(this.allowCreate) {
          return option.indexOf(this.keyword) > -1
        }else{
          return option[this._optionsKey.label].indexOf(this.keyword) > -1
        }
      }else{
        return true
      }
    },
    clearSelect() {
      this.$emit('clear')
    }
  }
  // components: {
  //   ElSelectNew
  // }
}
</script>



