Tooltip.props.openDelay = {type: Number, default: 1000}
// 修复left定位负值
VuePopper.created = function(){
  this.$watch('showPopper', function(val) {
    if(val) {
      this.$nextTick(() => {
        if(this.$refs.popper && this.$refs.popper.style.left[0] === '-') {
          this.$refs.popper.style.left = 0
        }
      })
    }
  })
}
