Input.props.size = {type: String, default: 'small'}
Select.props.size = {type: String, default: 'small'}
Cascader.props.size = {type: String, default: 'small'}
Button.props.size = {type: String, default: 'small'}


// 组件全局设置
Input.mounted = function() {
  let maxlength = this.$attrs.maxlength
  if(maxlength){
    let $span = $('<span class="input-maxlength__limit">').html(`0/${maxlength}`)
    let input = this.$refs.input || this.$refs.textarea
    // + 1 为 /符号  20 5左间距15右间距
    input.style.paddingRight = ((maxlength + '').length * 2 + 1) * 7 + 20 + 'px'
    $(this.$el).append($span)
    this.$watch('value', function(val) {
      val = val || ''
      $span.html(`${val.length}/${maxlength}`)
    }, { immediate: true })
  }
}

Dialog.props.closeOnClickModal = {type: Boolean, default: false}

// TableColumn.props.headerAlign = { type: String, default: 'center' }
Table.props.size = {type: String, default: 'medium'}
Table.props.rowKey = {type: [String, Function], default: 'id'}
