DatePicker.props.placeholder = {type: String, default: '请选择日期'}

Select.beforeMount = function() {
  // 多选下拉框-有值时第一个选项滚动到视野内
  if(this.multiple) {
    this.$on('visible-change', visible => {
      if(visible) {
        this.$nextTick(() => {
          let scrollbar = this.$refs.scrollbar
          let scrollWrap = scrollbar.$refs.wrap
          let ele = $(scrollbar.$el).find('.selected')[0]
          scrollWrap.addEventListener('scroll', () => {
            if(ele) {
              let maxMoveY = ele.offsetTop
              let minMoveY = maxMoveY + ele.offsetHeight - ele.offsetParent.offsetHeight
              if(scrollWrap.scrollTop > maxMoveY || scrollWrap.scrollTop < minMoveY) {
                scrollWrap.scrollTop = minMoveY
              }
              ele = null
            }
          })
        })
      }
    })

    this.$on('hook:updated', () => {
      if(this.$el.className.indexOf('disable-tag') > -1) {
        let _this = this
        // todo 仅分组管理编辑页用到 待优化
        $(this.$el).find('.el-tag').each((index, t) => {
          if(_this.selected[index] && _this.selected[index].disabled) {
            $(t).addClass('unclosed-tag')
          }
        })
      }
    })
  }
}

Vue.use(Element)
