/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  data() {
    return {
      isScience: !!document.getElementById('isScience'),
      // 地区叶子节点标识
      areaLeaf: 'S',
      // 接口成功code
      codeSuccess: SUCCESS,
      // IfElseCondition id
      baseIdGetter: 999,

      reletLoading: false,
      reletTimer: null
    }
  },
  computed: {
    isAreaUser() {
      return this.$store.state.main.userInfo.roleName === 'region.admin' || this.$store.state.main.userInfo.roleName === 'sys.admin'
    },
    isStreetUser() {
      return this.$store.state.main.userInfo.roleName === 'street.admin'
    }
  },
  mounted() {
    document.addEventListener('input', () => {
      this.relet()
    })
    document.addEventListener('click', () => {
      this.relet()
    })
  },
  methods: {
    relet(n = 15) {
      // 页面又点击/输入操作 默认 15 分钟续租一次
      // if(this.$route.name !== 'Login' && this.latestRequestTimestamp + n * 60 * 1000 < new Date()){
      //   this.$http.get(reletApi, '', { customMessage: true })
      // }
    },
    fetchRelet() {
      // 6 分钟自动续租  (比15小即可)
      // const a = sessionStorage.reletCycleTime || 6
      const a = 6
      this.reletTimer = setInterval(() => {
        this.relet(a, true)
      }, a * 60 * 1000)
    }
  },
  template: '<App/>',
  components: { App }
})



