<template>
  <el-dialog title="退回记录"
             width="500px"
             :visible.sync="dialogVisible"
             :before-close="cancel"
             v-loading="loading">
    <el-table :data="tableData"
              stripe
              border>
      <el-table-column prop="createBy"
                       label="操作用户"
                       show-overflow-tooltip></el-table-column>
      <el-table-column prop="reason"
                       label="退回原因"
                       show-overflow-tooltip></el-table-column>
      <el-table-column prop="createTime"
                       label="操作时间"
                       width="160"></el-table-column>
    </el-table>
  </el-dialog>
</template>
<script>
export default {
  name: 'ReportComplainReturnRecord',
  mixins: [],
  props: {
    viewData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      dialogVisible: true,
      loading: false,
      tableData: []
    }
  },
  computed: {},
  watch: {},
  created() {
    this.getHistory()
  },
  methods: {
    getHistory(){
      this.loading = true
      this.$http.get('/informs/refund/list/' + this.viewData.id).then(res => {
        if (res.code === this.$root.codeSuccess) {
          this.tableData = res.content
        }
        this.loading = false
      })
    },
    cancel() {
      this.$emit('close')
    }
  },
  beforeDestroy() {},
  components: {}
}
</script>




