import downloadFileByIframeFun from '@/utils/downloadFile.js'
import html2Canvas from 'html2canvas'
import JsPDF from 'jspdf'
/**
 * 全局下载文件方法
 */
export const downloadFileByIframe = {
  install(Vue) {
    Object.defineProperty(Vue.prototype, '$downloadFileByIframe', {
      value: downloadFileByIframeFun
    })
  }
}
/**
 * 表单校验后校验出错项滚动到视野内
 */
export const errorIntoView = {
  install: function(Vue) {
    Vue.prototype.$errorIntoView = function() {
      // 校验有延迟
      setTimeout(() => {
        let ele = $(this.$el).find('.is-error')
        if (ele) {
          ele = ele.find('.el-input__inner,.el-textarea__inner')[0]
        }
        ele = ele || $(this.$el).find('.el-form-item__error')[0]
        if (ele) {
          ele.scrollIntoView()
        }
      }, 500)
    }
  }
}
/**
 * 自定义确认提示框
 */
export const confirmTags = {
  install: function(Vue) {
    Vue.prototype.$confirmTags = function({
      title = '提示',
      type = 'warning',
      showCancelButton = true,
      showConfirmButton = true,
      customContent,
      confirmButtonText = '确定',
      cancelButtonText = '取消',
      list = [],
      beforeClose
    }) {
      const h = this.$createElement
      let template
      if (customContent) {
        template = customContent
      } else {
        const divStyle = {
          class: 'flex-box-center wrap'
        }
        template = []
        list.forEach(msg => {
          if (msg.tags && msg.tags.length) {
            let messageTemple = [
              h('p', { class: msg.class || '' }, msg.title),
              h(
                'div',
                divStyle,
                msg.tags.map(t => h('el-tag', { props: { size: 'mini', type: msg.tagType || '' } }, t))
              )
            ]
            template = template.concat(messageTemple)
          }
        })
      }
      return this.$msgbox({
        title,
        type,
        customClass: 'message-box__custom',
        message: h('el-scrollbar', {
          props: {
            wrapClass: 'message-box__wrap',
            viewClass: 'message-box__view'
          }
        }, template),
        showCancelButton,
        showConfirmButton,
        confirmButtonText,
        cancelButtonText,
        beforeClose
      })
    }
  }
}
/**
 *  树结构数据扁平化
 */
function flatArrayFun({list, children, arr, delFlag, setIdFlag, delIdFlag, idKey = 'id'}) {
  list.forEach(l => {
    let _l = l
    if (l[children] && l[children].length) {
      if (delFlag) {
        _l = _.cloneDeep(l)
        delete _l[children]
      }
      flatArrayFun({
        list: l[children],
        children,
        arr,
        delFlag,
        setIdFlag,
        delIdFlag,
        idKey
      })
    }
    if(setIdFlag) {
      l[idKey] = ++this.$root.baseIdGetter
    }else if(delIdFlag){
      delete l[idKey]
    }
    if(arr) {
      arr.push(_l)
    }
  })
}
export const flatArray = {
  install: function(Vue) {
    Vue.prototype.$flatArray = flatArrayFun
  }
}
/**
 * [{label:xx,value:xxx}] 转 obj
 */
export const optionsToObject = {
  install: function(Vue) {
    Vue.prototype.$optionsToObject = function(options, key = 'value', value = 'label') {
      let obj = {}
      options.forEach(o => {
        obj[o[key]] = o[value]
      })
      return obj
    }
  }
}
/**
 * [{label:xx,value:xxx}] 转 [{text:xx,value:xx}]
 */
export const optionsToFilters = {
  install: function(Vue) {
    Vue.prototype.$optionsToFilters = function(options, text = 'label', value = 'value') {
      return options.map(o => ({
        text: o[text],
        value: o[value]
      }))
    }
  }
}
/**
 * 复制指定内容到剪切板上
 */
export const copyValueToClipboard = {
  install: function(Vue) {
    Vue.prototype.$copyValueToClipboard = function(value) {
      let $input = document.getElementById('copy-input__disN')
      let confirm = $('.el-message-box__wrapper').filter(function(index){
        return this.style.display !== 'none'
      })
      confirm.hide()
      $input.value = value
      $input.select()
      document.execCommand('copy')
      confirm.show()
    }
  }
}
/**
 * 序列化查询参数
 */
export const paramsSerializer = {
  install: function(Vue) {
    Vue.prototype.$paramsSerializer = function(params) {
      let result = ''
      for(let key in params) {
        if(params[key] !== null && params[key] !== undefined && params[key] !== '') {
          result += key + '=' + params[key] + '&'
        }
      }
      return '?' + result.slice(0, -1)
    }
  }
}

export const htmlToPdf = {
  install: function(Vue) {
    Vue.prototype.$htmlToPdf = function(idStr, title = 'PDF文档') {
      return new Promise(resolve => {
        document.body.scrollTop = 0
        document.body.scrollLeft = 0
        document.documentElement.scrollLeft = 0
        document.documentElement.scrollTop = 0
        window.pageYOffset = 0
        const targetDom = document.querySelector('#' + idStr) // 获取要截图的元素

        const eleW = targetDom.offsetWidth// 获得该容器的宽
        const eleH = targetDom.scrollHeight// 获得该容器的高
        const eleOffsetTop = targetDom.offsetTop// 获得该容器到文档顶部的距离
        const eleOffsetLeft = targetDom.offsetLeft// 获得该容器到文档最左的距离
        var canvas = document.createElement('canvas')
        var abs = 0
        const winIn = document.documentElement.clientWidth || document.body.clientWidth// 获得当前可视窗口的宽度（不包含滚动条）
        const winOut = window.innerWidth// 获得当前窗口的宽度（包含滚动条）
        if (winOut > winIn) {
          abs = (winOut - winIn) / 2// 获得滚动条宽度的一半
        }
        canvas.width = eleW * 2// 将画布宽&&高放大两倍
        canvas.height = eleH * 2
        var context = canvas.getContext('2d')
        context.scale(2, 2) // 增强图片清晰度
        context.translate(-eleOffsetLeft - abs, -eleOffsetTop)

        var width = targetDom.offsetWidth / 4
        var height = targetDom.offsetHeight / 4

        html2Canvas(document.querySelector('#' + idStr), {
          width: targetDom.scrollWidth,
          height: targetDom.scrollHeight,
          useCORS: true,
          scrolly: 0,
          scrollx: 0
        }).then(function(canvas) {
          const pageData = canvas.toDataURL('image/jpeg', 1.0)

          if (width > height) {
            // 此可以根据打印的大小进行自动调节
            // eslint-disable-next-line
            var pdf = new JsPDF('l', 'mm', [
              width * 0.505,
              height * 0.545
            ])
          } else {
            // eslint-disable-next-line
            var pdf = new JsPDF('p', 'mm', [
              width * 0.505,
              height * 0.545
            ])
          }
          pdf.addImage(
            pageData,
            'jpeg',
            0,
            0,
            width * 0.505,
            height * 0.545
          )
          pdf.save(title + '.pdf')
          // document.getElementById('downloadFrame').src = pageData
          resolve()
        })
      })
    }
  }
}



