import Vue from 'vue'
import Vuex from 'vuex'
import main from './modules/main'
Vue.use(Vuex)
// 严格模式 vuex
// const debug = process.env.NODE_ENV !== 'production'
const debug = false

export default new Vuex.Store({
  modules: {
    main
  },
  strict: !debug
})




