import axios from 'axios'
import { Message } from 'element-ui'
import {
  SUCCESS,
  NEED_RELOGIN,
  NEED_LOGIN,
  NO_AUTHORIZATION
} from '@/constants/response-code'
// import { MAIN_SET_LATEST_REQUEST_TIMESTAMP } from '@/store/types/main.js'
import downloadFileByIframeFun from '@/utils/downloadFile.js'
// import router from '@/router'
// require('es6-promise/auto')
// require('promise.prototype.finally').shim()
// const store = require('@/store')
const router = require('@/router')
const isDevEnv =
  window.location.href.indexOf('http://192.168') !== -1 ||
  window.location.href.indexOf('http://localhost') !== -1

let api = '/financial-risk'
// let promiseMap = {}
const CancelToken = axios.CancelToken

// axios.defaults.timeout = 10000
axios.interceptors.request.use(
  function(config) {
    if (config.url) {
      config.url = api + config.url
    } else {
      console.error('请求出错' + location.href)
    }
    // if (promiseMap[config.url]) {
    //   promiseMap[config.url]('操作取消,url:' + config.url)
    //   promiseMap[config.url] = null
    // } else {
    //   promiseMap[config.url] = cancel
    // }
    if (config.method === 'get') {
      if (!config.params) {
        config.params = {}
      }else{
        config.params = {...config.params}
      }
      config.params._ = new Date().getTime()
    }
    if (!isDevEnv) {
      //   config.withCredentials = true
      //   // 匹配字符串添加前缀
      //   try {
      //     let _match = config.url
      //     .match(/^\/api\/(adm|erp|forum)/gi)[0]
      //     let matchKey = config.url
      //       .match(/^\/api\/(adm|erp|forum)/gi)[0]
      //       .replace('/api/', '')
      //     if (!matchKey) {
      //       throw new Error(`没匹配到对应的路径${config.url}`)
      //     }
      //     config.url = config.url.replace(_match, baseURL[matchKey])
      //     console.log(config.url)
      //     // config.baseURL = baseURL[matchKey]
      //   } catch (e) {
      //     console.error(e.toString())
      //   }
    }
    // 判断是否存在token，如果存在，则为每个header都加上token
    if (window.localStorage.getItem('accessToken')) {
      config.headers.Authorization = window.localStorage.getItem('accessToken')
    }

    return config
  },
  error => {
    return Promise.reject(error)
  }
)
axios.interceptors.response.use(
  response => {
    if (!response.config.customMessage && response.data.code !== SUCCESS) {
      Message.warning(response.data.errMsg)
    }
    if (
      response.data.code === NEED_RELOGIN ||
      response.data.code === NEED_LOGIN ||
      response.data.code === NO_AUTHORIZATION
    ) {
      // 302 401 403 需要登录
      router.default.push('/login')
      // window.location = `login.html?_=${new Date().getTime()}`
    }
    // 续租
    if (response.data.code === SUCCESS) {
      // store.default.commit(MAIN_SET_LATEST_REQUEST_TIMESTAMP)
    }
    return response.data
  },
  error => {
    if (
      error.code === 'ECONNABORTED' &&
      error.message.indexOf('timeout') !== -1
    ) {
      Message.error('请求超时')
    }
    if (error && error.response) {
      let str = '出现网络错误,请重试'
      switch (error.response.status) {
        case 400:
          str = '请求错误'
          break
        case 401:
          str = '请登录'
          break
        case 403:
          str = '未授权'
          break
        case 404:
          str = `请求地址不存在`
          break
        case 408:
          str = '请求超时'
          break
        case 500:
          str = '服务器异常'
          break
        case 501:
          str = '服务未实现'
          break
        case 502:
          str = '网关错误'
          break
        case 503:
          str = '服务不可用'
          break
        case 504:
          str = '网关超时'
          break
        case 505:
          str = 'HTTP版本不受支持'
          break
        default:
      }
      if (process.env.NODE_ENV === 'production') {
        // 登录页不做跳转处理咯
        if (window.location.href.indexOf('login') !== -1) {
        } else if (
          error.response.status === NEED_RELOGIN ||
          error.response.status === NEED_LOGIN ||
          error.response.status === NO_AUTHORIZATION
        ) {
          router.default.push('/login')
        } else {
          router.default.push({
            name: 'Error',
            params: {
              statusCode: error.response.status,
              message: str
            }
          })
        }
      }
      Message.error(str)
    } else {
      // 是否手动取消请求
      if (axios.isCancel(error)) {
        console.log(error)
      } else {
        Message.error('出现网络错误,请重试')
      }
    }
    return Promise.resolve((error.response && error.response.data) || (error.response || {}))
  }
)
function setCancelRequest(config) {
  if (config && typeof config.cancelRequest === 'function') {
    config.cancelToken = new CancelToken(c => {
      config.cancelRequest(c)
    })
  }
}
function axiosMethod(url, data, config, method) {
  setCancelRequest(config)
  return new Promise((resolve, reject) => {
    let httConfig = {
      method,
      url,
      data,
      ...config
    }
    if (Object.prototype.toString.call(data) === '[object FormData]') {
      httConfig.headers = {
        'Content-Type': 'multipart/form-data'
      }
    }
    axios(httConfig).then(
      res => {
        resolve(res)
      },
      err => {
        err.isError = true
        resolve(err)
      }
    )
  })
}

export const $http = {
  get(url, params, config = {}) {
    setCancelRequest(config)
    return new Promise((resolve, reject) => {
      // for(let key in params) {
      //   params[key] = encodeURIComponent(encodeURIComponent(params[key]))
      // }
      // 检索关键字加密
      if (params && params.keyword) {
        params = _.cloneDeep(params)
        params.keyword = encodeURIComponent(encodeURIComponent(params.keyword))
      }
      axios({
        method: 'get',
        url,
        params,
        ...config
      }).then(
        res => {
          resolve(res)
        },
        err => {
          reject(err)
        }
      )
    })
  },
  post(url, data, config = {}) {
    return axiosMethod(url, data, config, 'post')
  },
  put(url, data, config = {}) {
    return axiosMethod(url, data, config, 'put')
  },
  delete(url, data, config = {}) {
    setCancelRequest(config)
    return new Promise((resolve, reject) => {
      axios({
        method: 'delete',
        url,
        data,
        ...config
      }).then(
        res => {
          resolve(res)
        },
        err => {
          reject(err)
        }
      )
    })
  },
  export(url, data, config = {}) {
    setCancelRequest(config)
    return new Promise((resolve, reject) => {
      let httConfig = {
        method: 'post',
        url,
        data,
        ...config
      }
      axios(httConfig).then(
        res => {
          if (res.code === SUCCESS) {
            downloadFileByIframeFun(res.data, '').then(res => {
              resolve(res)
            })
          } else {
            resolve(res)
          }
        },
        err => {
          reject(err)
        }
      )
    })
  }
}

export default {
  install(Vue) {
    Object.defineProperty(Vue.prototype, '$http', {
      value: $http
    })
  }
}



