import { Message } from 'element-ui'
import { SUCCESS } from '@/constants/response-code'

export default function downloadFileByIframe(url, baseApi = '/financial-risk') {
  let iframe = document.getElementById('downloadFrame')
  let timestamp = +new Date() + ''
  let str = url.indexOf('?') > -1 ? '&' : '?'
  iframe.src = baseApi + url + str + '_=' + timestamp
  return new Promise(resolve => {
    let data = {}
    let timer = setInterval(() => {
      if(document.cookie.indexOf(timestamp) > -1) {
        let d = new Date()
        d.setDate(d.getDate() - 1)
        let arr = document.cookie.split('; ')
        for(let i = 0, len = arr.length; i < len; i++){
          let item = arr[i].split('=')
          if(item[0] === timestamp){
            let res = item[1].split('_')
            data.code = res[0] * 1
            data.message = res[1] || ''
            break
          }
        }
        let status = data.code === SUCCESS
        data.exportStatus = status
        Message[status ? 'success' : 'warning'](data.message)
        resolve(data)
        document.cookie = timestamp + '=' + '' + ';expires=' + d
        clearInterval(timer)
        timer = null
      }
    }, 200)
    setTimeout(() => {
      if(timer) {
        clearInterval(timer)
        resolve(data)
        // 所有下载均用cookie返回请求信息时提示
        // Message.warning('请重新下载')
      }
    }, 5000)
  })
}




