import Vue from 'vue'
import Vuex from 'vuex'
import * as MAIN_CONSTANTS from '../types/main.js'
import SUCCESS from '../../constants/response-code.js'

import { $http } from '../../configs/axios.js'
// import { Message } from 'element-ui'

Vue.use(Vuex)

const state = {
  userMenu: sessionStorage.Menu ? JSON.parse(sessionStorage.userMenu) : [],
  userInfo: sessionStorage.userInfo ? JSON.parse(sessionStorage.userInfo) : {}
}

const actions = {
  [MAIN_CONSTANTS.MAIN_USER_INFO_GET](context) {
    let promise = $http.get('/users/info')
    promise.then(res => {
      if (res.code === SUCCESS) {
        context.commit(MAIN_CONSTANTS.MAIN_USER_INFO_SET, res.content)
      }
    })
    return promise
  },
  [MAIN_CONSTANTS.MAIN_USER_MENU_GET](context) {
    let promise = $http.get('/menus')
    promise.then(res => {
      if (res.code === SUCCESS) {
        context.commit(MAIN_CONSTANTS.MAIN_USER_MENU_SET, res.content)
      }
    })
    return promise
  }
}

const mutations = {
  [MAIN_CONSTANTS.MAIN_USER_INFO_SET](state, data) {
    state.userInfo = data
    sessionStorage.userInfo = JSON.stringify(data)
  },
  [MAIN_CONSTANTS.MAIN_USER_MENU_SET](state, data) {
    state.userMenu = data
    sessionStorage.userMenu = JSON.stringify(data)
  }
}

const getters = {}
export default {
  state,
  actions,
  getters,
  mutations
}



