<template>
  <div class="home-page-container" style="overflow: auto">
    <div class="home-top">
      <div class="top-left">
        <company-map></company-map>
        <history-increase></history-increase>
      </div>
      <div class="top-right">
        <street-company-num></street-company-num>
        <business-status></business-status>
      </div>
    </div>
    <div class="home-bottom">
      <patent-rank></patent-rank>
      <soft-rank></soft-rank>
      <business-penalty-rank></business-penalty-rank>
    </div>
  </div>
</template>
<script>
  import CompanyMap from './echarts-components/CompanyMap'
  import HistoryIncrease from './echarts-components/HistoryIncrease'
  import StreetCompanyNum from './echarts-components/StreetCompanyNum'
  import BusinessStatus from './echarts-components/BusinessStatus'
  import PatentRank from './echarts-components/PatentRank'
  import BusinessPenaltyRank from './echarts-components/BusinessPenaltyRank'
  import SoftRank from './echarts-components/SoftRank'
  export default {
    title: '首页',
    components: {
      CompanyMap,
      HistoryIncrease,
      StreetCompanyNum,
      BusinessStatus,
      PatentRank,
      BusinessPenaltyRank,
      SoftRank
    },
    data () {
      return {}
    }
  }
</script>



