<template>
  <el-tooltip :content="_content"
              :placement="placement"
              :disabled="disabled">
    <slot slot="content"
          name="content" />
    <div class="ellipsis"
         :class="_className"
         @click="click">
      <slot />{{text}}
      <slot name="suffix" />
    </div>
  </el-tooltip>
</template>
<script>
/**
 * 文字溢出... hover 显示组件
 */
export default {
  name: 'TipText',
  props: {
    placement: {
      type: String,
      default: 'top-start'
    },
    /**
     * 提示内容
     */
    content: {
      default: ''
    },
    /**
     * 文本内容
     */
    text: {
      default: ''
    },
    className: {
      type: [String, Object],
      default: ''
    },
    /**
     * 可点击文字
     */
    btnText: {
      type: Boolean,
      default: false
    },
    /**
     * 响应页面 reszie 事件 -- 宽度固定传 false 已提高性能
     */
    resize: {
      type: Boolean,
      default: true
    },
    /**
     * 多行溢出
     */
    multiLine: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      disabled: true,
      timer: null,
      textWidth: 0
    }
  },
  computed: {
    _content() {
      return this.content || this.text
    },
    _className() {
      if(this.btnText) {
        if(typeof this.className === 'string') {
          if(this.className) {
            return this.className + ' btn-text'
          }else{
            return 'btn-text'
          }
        }else{
          return {
            ...this.className,
            'btn-text': true
          }
        }
      }else{
        return this.className
      }
    }
  },
  watch: {
    text() {
      console.log(1111111)
      this.setDisabled()
    }
  },
  mounted() {
    this.setDisabled()
    if(this.resize) {
      window.addEventListener('resize', this.setDisabled)
    }
  },
  methods: {
    setTextWidth() {
      if(!this.multiLine && !this.$dom) {
        let $dom = $('<span>')
        $dom.html(this.$el.innerHTML).css({
          visibility: 'hidden',
          position: 'fixed',
          left: 0,
          top: 0
        }).appendTo((this.$el))

        this.$dom = $dom
      }
    },
    setDisabled(){
      this.setTextWidth()
      clearTimeout(this.timer)
      this.timer = setTimeout(() => {
        if(!this.multiLine && this.$dom) {
          this.textWidth = this.$dom.width()
          this.$dom.remove()
          this.$dom = null
        }
        // 10 的意思是行高最小12 10即可 高度渲染有误差 一行字18px 渲染出来可能是19px
        // 内容实际宽度小于div可视宽度禁用hover
        this.disabled = !this.$el || (this.multiLine ? this.$el.scrollHeight - this.$el.clientHeight < 10 : this.textWidth < this.$el.clientWidth)
      }, 100)
    },
    click() {
      this.$emit('click')
    }
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.setDisabled)
  }
}
</script>



