<template>
  <el-dialog v-loading="loading"
             title="分配任务"
             width="400px"
             :visible.sync="dialogVisible"
             :before-close="cancel">
    <el-form ref="form"
             :model="form"
             :rules="rules"
             label-width="80px">
      <el-form-item prop="areaId"
                    label="分配街道">
        <el-select-mine v-model="form.areaId"
                        :optionsKey="{
                          label: 'areaName',
                          value: 'id'
                        }"
                        :options="areaList"
                        class="table-query-content"
                        clearable />
      </el-form-item>
      <el-form-item prop="opMessage"
                    label="分配意见">
        <el-input type="textarea"
                  :rows="4" v-model="form.opMessage"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer">
      <el-button type="primary"
                 @click="submit"
                 :loading="saving">确定分配</el-button>
    </div>
  </el-dialog>
</template>
<script>
export default {
  name: 'CheckDisposeAllot',
  props: {
    viewData: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      loading: false,
      dialogVisible: true,
      saving: false,
      form: {
        areaId: ''
      },
      areaList: [],
      rules: {
        areaId: [
          {
            required: true, message: '请选择', trigger: 'change'
          }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created() {
    this.getArea()
  },
  methods: {
    getArea() {
      this.$http.get('/areas/getDispatcherAreaList')
      .then(res => {
        if(res.code === this.$root.codeSuccess) {
          this.areaList = res.content
        }
      })
    },
    submit(){
      this.$refs.form.validate(valid => {
        if(valid){
          this.saving = true
          this.$http.put('/verifies/dispatcher/' + this.viewData.id, this.form).then(res => {
            if (res.code === this.$root.codeSuccess) {
              this.cancel()
              this.$emit('reload')
              this.$message.success('分配成功')
            }
            this.saving = false
          })
        }
      })
    },
    cancel() {
      this.$emit('close')
    }
  },
  beforeDestroy() {},
  components: {}
}
</script>



