<template>
  <div>
    <div class="current-router-title">{{menuTitle}}</div>
    <div class="table-container">
      <div class="border background-white">
        <div class="sub-title">总体统计</div>
        <div v-loading="chartLoading"
             class="chart-box">
          <div class="chart-title">
            <div class="chart-name">举报任务和核查任务完成情况的总体统计</div>
            <div class="chart-legend">
              <span class="legend-1">举报任务</span>
              <span class="legend-2">核查任务</span>
            </div>
            <div class="chart-query el-form-item--small">
              <label class="el-form-item__label"
                     style="width: 80px;">时间范围</label>
              <div class="el-form-item__content"
                   style="margin-left: 80px;">
                <el-date-time-range-two-value-picker :value1="chartParams.startDate"
                                                     :value2="chartParams.endDate"
                                                     class="el-date-two-value-picker"
                                                     :clearable="false"
                                                     format="yyyy-MM-dd"
                                                     valueFormat="yyyy-MM-dd"
                                                     @changeValue="changeChartParams" />
              </div>
            </div>
          </div>
          <div ref="chartTarget"
               class="chart-target"></div>
        </div>
      </div>
      <div class="bound-bar"></div>
      <div class="table-box">
        <div class="background-white flex-1">
          <div class="border sub-title">待办事项</div>
          <el-table :show-header="false"
                    :data="tableData1"
                    border
                    row-key="">
            <el-table-column width="40">
              <template slot-scope="{}"><i class="el-icon-edit-outline"></i></template>
            </el-table-column>
            <el-table-column prop="name"></el-table-column>
            <el-table-column prop="value">
              <template slot-scope="scope">
                <div class="num-span">{{ scope.row.value }}</div>
                <!-- <el-button type="warning"
                           round
                           readonly>
                  {{ scope.row.value }}
                </el-button> -->
              </template>
            </el-table-column>
            <!-- <el-table-column width="180"
                             prop=""></el-table-column> -->
          </el-table>
        </div>
        <div class="bound-bar-vertical"></div>
        <div class="background-white flex-1">
          <div class="border sub-title">举报对象TOP10</div>
          <el-table :max-height="600"
                    :data="tableData2"
                    border
                    row-key="">
            <el-table-column width="50"
                             type="index"
                             label="排名"></el-table-column>
            <el-table-column label="对象"
                             show-overflow-tooltip
                             prop="entName"></el-table-column>
            <el-table-column width="120"
                             label="举报次数"
                             prop="informNum"></el-table-column>
          </el-table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
const echarts = require('echarts/lib/echarts')
require('echarts/lib/component/toolbox')
require('echarts/lib/component/tooltip')
require('echarts/lib/component/grid')
require('echarts/lib/component/legend')
require('echarts/lib/chart/bar')

// import * as echarts from 'echarts/core'
// import {
//     GridComponent
// } from 'echarts/components'
// import {
//     BarChart
// } from 'echarts/charts'
// import {
//     CanvasRenderer
// } from 'echarts/renderers'
export default {
  name: 'statistics',
  mixins: [],
  props: {
    menuTitle: {
      type: String,
      required: true
    }
  },
  data() {
    const that = this
    return {
      chartLoading: false,

      chartParams: {
        startDate: '',
        endDate: ''
      },
      myChart: null,
      chartOptions: {
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            // type: 'cross',
            // crossStyle: {
            //   color: 'red'
            // }
          },
          formatter: function (params, ticket, callback) {
            // console.log(params, ticket)
            let source = that.chartSourceData[params[0].dataIndex]
            let total = params[0].data + params[1].data
            let doneNum = source.informChecked + source.taskChecked
            return `<div>${source.areaName}` +
                      `<div>任务总数: ${total || 0}</div>` +
                      `<div>已完成: ${doneNum || 0}</div>` +
                      `</div>未完成: ${(source.informUncheck + source.taskUncheck) || 0}<div>` +
                      `</div>完成率: ${(((doneNum / total) || 0) * 100).toFixed(2)}%<div>` +
                    '</div>'
          }
        },
        // toolbox: {
        //   feature: {
            // dataView: {show: true, readOnly: false},
            // magicType: {show: true, type: ['bar']},
            // restore: {show: true}
            // saveAsImage: {show: true}
        //   }
        // },
        // legend: {
        //   data: ['举报任务', '核查任务']
        // },
        xAxis: {
          name: '行政区域',
          type: 'category',
          data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'],
          splitArea: {
            interval: 100
          },
          axisTick: {
            alignWithLabel: true
          },
          axisLabel: {
            // rotate: 30,
            // width: 50,
            // overflow: 'breakAll',
            interval: 0,
            lineHeight: 16,
            color: '#666'
          },
          axisPointer: {
            type: 'shadow'
          }
          // interval: 200
        },
        yAxis: {
          name: '数量(条)',
          // min: 10,
          minInterval: 1,
          type: 'value'
        },
        series: [
          {
            name: '举报任务',
            data: [120, 200, 150, 80, 70, 110, 130],
            type: 'bar',
            showBackground: true,
            backgroundStyle: {
              color: '#F1F1F1'
            },
            itemStyle: {
              color: '#417FFF'
            },
            barWidth: 15,
            barGap: '25%',
            barCategoryGap: 100
          },
          {
            name: '核查任务',
            data: [50, 100, 10, 180, 170, 210, 100],
            type: 'bar',
            showBackground: true,
            backgroundStyle: {
              color: '#F1F1F1'
            },
            itemStyle: {
              color: '#58CAFF'
            },
            barWidth: 15,
            barGap: '25%',
            barCategoryGap: 100
          }
        ]
      },
      chartSourceData: [],
      tableData1: [],
      tableData2: [],
      tableLoading1: false,
      tableLoading2: false
    }
  },
  computed: {},
  created() {},
  mounted() {
    this.initTableData()
  },
  methods: {
    changeChartParams(v) {
      this.chartParams.startDate = v[0]
      this.chartParams.endDate = v[1]
      if(!this.myChart) {
        this.myChart = echarts.init(this.$refs.chartTarget)
      }
      this.chartLoading = true
      this.$http.get('/report/statisticalgraph', this.chartParams).then(res => {
        if(res.code === this.$root.codeSuccess) {
          let data = res.content.graphItemList
          let reg = /.{4}/g
          // this.chartOptions.xAxis.data = data.map(r => r.areaName)
          this.chartOptions.xAxis.data = data.map(r => ({
            value: r.areaName.replace(reg, function($0, $1){
              return $0 + '\n'
            })
            // value: r.areaName,
            // textStyle: {
            //   width: 20,
            //   color: 'red',
            //   fontSize: 12,
            //   overflow: 'breakAll'
            // }
          }))

          this.chartOptions.series[0].data = data.map(r => r.informChecked + r.informUncheck)
          this.chartOptions.series[1].data = data.map(r => r.taskChecked + r.taskUncheck)

          this.chartOptions = this.chartOptions
          this.chartSourceData = data
          this.myChart.setOption(this.chartOptions)
        }
        this.chartLoading = false
      })
    },
    initTableData() {
      this.tableLoading1 = this.tableLoading2 = true
      this.$http.get('/report/pendinglist').then(res => {
        if(res.code === this.$root.codeSuccess) {
          this.tableData1 = [
            {
              name: '逾期的举报线索总数',
              value: res.content.informOverdueNum
            },
            {
              name: '待核查的举报线索总数',
              value: res.content.informToCheckNum
            },
            {
              name: '逾期的核查任务总数',
              value: res.content.taskOverdueNum
            },
            {
              name: '待核查的核查任务总数',
              value: res.content.taskToCheckNum
            }
          ]
        }
        this.tableLoading1 = false
      })
      this.$http.get('/report/informstop').then(res => {
        if(res.code === this.$root.codeSuccess) {
          this.tableData2 = res.content
        }
        this.tableLoading2 = false
      })
    }
  },
  beforeDestroy() {},
  components: {}
}
</script>
<style lang="scss">
.chart-box {
  padding: 10px 20px 20px;
}
.chart-title {
  display: flex;
  justify-content: space-between;
  line-height: 32px;
  height: 32px;
  margin-bottom: 20px;
  .chart-name {
    font-size: 13px;
    color: #333333;
  }
  .chart-legend {
    display: flex;
    flex-direction: rows;
    span {
      position: relative;
      padding-left: 20px;
      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 9px;
        width: 14px;
        height: 14px;
      }
    }
    .legend-1::before {
      background: #417FFF;
    }
    .legend-2::before {
      background: #58CAFF;
    }
  }
}
.el-date-two-value-picker {
  width: 200px!important;
}
.chart-target {
  height: 300px;
  overflow: auto;
}
.table-box {
  display: flex;
  overflow: hidden;
  .border {
    border-bottom: none;
  }
  .background-white {
    flex: 1;
    overflow: hidden;
    &:first-of-type {
      margin-right: 5px;
    }
    &:last-of-type {
      margin-left: 5px;
    }
  }
}
.num-span {
  padding: 0 10px;
  border-radius: 10px;
  text-align: center;
  background: #EEBA37;
  color: #fff;
}
</style>




