<template>
  <el-dialog title="导入文件"
             width="300px"
             :visible.sync="dialogVisible"
             :before-close="cancel">
    <el-form ref="form"
             :model="form"
             :rules="rules">
      <el-form-item prop="fileList">
        <el-upload action=""
                   :auto-upload="false"
                   :file-list="form.fileList"
                   accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
                   :on-change="handleChange"
                   :on-remove="handleRemove">
          <div class="el-upload__tip">
            <el-button round
                       slot="trigger"
                       size="mini"
                       @click="form.fileList=[]">选择文件</el-button>
            支持xls/xlsx格式
          </div>
        </el-upload>
      </el-form-item>
    </el-form>
    <div slot="footer">
      <el-button @click="cancel">取消</el-button>
      <el-button type="primary"
                 @click="submit"
                 :loading="saving">确定</el-button>
    </div>
  </el-dialog>
</template>
<script>
export default {
  name: 'ReportComplainImport',
  mixins: [],
  props: {},
  data() {
    const reg = /\.xlsx?$/
    return {
      dialogVisible: true,
      saving: false,
      form: {
        fileList: []
      },
      rules: {
        fileList: [
          {
            validator: (rule, value, callback) => {
              let file = value[0]
              if (!file) {
                callback(new Error('请选择文件'))
              } else if (!reg.test(file.name)) {
                callback(new Error('文件格式不符合要求'))
              // } else if (size && file.size > size * 1024 * 1024) {
              //   callback(new Error('文件大小超过导入限制'))
              } else {
                callback()
              }
            }
          }
        ]
      }
    }
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  methods: {
    handleChange(file, fileList) {
      this.form.fileList = fileList.map(file => file.raw)
      this.validateFile()
    },
    handleRemove(file, fileList) {
      this.form.fileList = fileList.map(file => file.raw)
      this.validateFile()
    },
    validateFile() {
      this.$nextTick(() => {
        this.$refs.form.validateField('fileList')
      })
    },
    submit(){
      this.$refs.form.validate(valid => {
        if(valid){
          this.saving = true
          let formData = new FormData()
          formData.append('file', this.form.fileList[0])
          this.$http.post('/informs/import', formData).then(res => {
            if (res.code === this.$root.codeSuccess) {
              this.cancel()
              this.$message.success('导入成功')
              this.$emit('reload')
            }
            this.saving = false
          })
        }
      })
    },
    cancel() {
      this.$emit('close')
    }
  },
  beforeDestroy() {},
  components: {}
}
</script>




