
<template>
  <el-date-picker v-model="_value"
                  :type="type"
                  range-separator="至"
                  start-placeholder="开始时间"
                  end-placeholder="结束时间"
                  :format="format"
                  :valueFormat="valueFormat"
                  :default-time="['00:00:00', '23:59:59']"
                  :size="size"
                  :disabled="disabled"
                  :clearable="clearable">
  </el-date-picker>
</template>
<script>
export default {
  name: 'ElDateTimeRangeTwoValuePicker',
  props: {
    value1: {
    },
    value2: {
    },
    size: {
      type: String,
      default: 'small'
    },
    disabled: {
      type: Boolean,
      default: false
    },
    clearable: {
      type: Boolean,
      default: true
    },
    format: {
      type: String,
      default: 'yyyy/MM/dd'
    },
    valueFormat: {
      type: String,
      default: 'yyyy/MM/dd'
    },
    type: {
      type: String,
      default: 'daterange'
      // default: 'datetimerange'
    }
  },
  data() {
    return {}
  },
  computed: {
    _value: {
      get() {
        return [this.value1 || '', this.value2 || '']
      },
      set(value) {
        this.$emit('changeValue', value || ['', ''])
        this.$emit('change', value || ['', ''])
      }
    }
  },
  beforeDestroy() {},
  components: {}
}
</script>



