import Login from '@/views/login/Login.vue'
import Main from '@/views/Main.vue'
import Statistics from '@/views/statistics/Statistics.vue'
import CheckDispose from '@/views/check_dispose/CheckDispose.vue'
import RiskTest from '@/views/risk_test/RiskTest.vue'
import ReportComplain from '@/views/report_complain/ReportComplain.vue'
import UserManage from '@/views/user_manage/UserManage.vue'
import HomePage from '@/views/home_page/HomePage.vue'
import CompanyManage from '@/views/company_manage/CompanyManage.vue'


export default [
  {
    path: '/login',
    title: '登陆',
    name: 'Login',
    component: Login
  },
  {
    path: '/main',
    name: 'Main',
    component: Main,
    children: [
      {
        path: 'statistics',
        name: 'Statistics',
        component: Statistics
      },
      {
        path: 'check_dispose',
        name: 'CheckDispose',
        component: CheckDispose
      },
      {
        path: 'risk_test',
        name: 'RiskTest',
        component: RiskTest
      },
      {
        path: 'report_complain',
        name: 'ReportComplain',
        component: ReportComplain
      },
      {
        path: 'user_manage',
        name: 'UserManage',
        component: UserManage
      },
      {
        path: 'home_page',
        name: 'HomePage',
        component: HomePage
      },
      {
        path: 'company_manage',
        name: 'CompanyManage',
        component: CompanyManage
      }
    ]
  }
]



