export default {
  data() {
    return {
      // 表格查询参数
      params: {
        pageNo: 1,
        pageSize: 10
      },
      defaultParams: {},
      // 表格数据
      table: {
        loading: false,
        data: [],
        total: 0
      },
      pageSizes: [10, 20, 50, 100]
    }
  },
  computed: {

  },
  created() {
    this.m_tableLoadData()
  },
  methods: {
    // 初始化表格
    m_tableLoadData() {
      this.table.loading = true
      this.$http.get(this.tableUrl, this.params).then(res => {
        // console.log(res)
        if (res.code === this.$root.codeSuccess) {
          this.table.data = res.content.data
          if(this.selectOperate) {
            this.selectOperate.tableData = this.table.data
            this.selectOperate.currentCheckedRows = []
          }
          this.table.total = res.content.count * 1
        }
        this.table.loading = false
      })
    },
    m_queryTable() {
      this.params.pageNo = 1
      this.m_tableLoadData()
    },
    m_resetTable() {
      for (const key in this.params) {
        if (key !== 'pageNo' && key !== 'pageSize') {
          this.params[key] = this.defaultParams[key] || ''
        }
      }
      this.m_tableLoadData()
    },
    m_handleChangeQuery(query, value) {
    },
    m_handleEnterQuery(queryView) {
    },
    m_handleQueryTable() {
    },
    // 修改每页数量
    m_handleSizeChange(limit) {
      this.params.pageSize = limit
      this.params.pageNo = 1
      this.m_tableLoadData()
      // }
    },
    // 修改页码
    m_handleCurrentChange(offset) {
      this.params.pageNo = offset
      this.m_tableLoadData()
    }
    // m_toggleRowSelection(){
    //   this.$nextTick(() => {
    //     if(this.checkboxTable && !this.withoutCheckboxTableFlag && this.$refs.table) {
    //       this.table.data.forEach(r => {
    //         let index = this.checkRows.findIndex(row => row.id === r.id)
    //         let selected = index > -1
    //         this.$refs.table.toggleRowSelection(r, selected)
    //         if(selected) {
    //           this.checkRows.splice(index, 1, r)
    //         }
    //       })
    //     }
    //   })
    // },
    // m_clearCheck() {
    //   this.checkRows = []
    //   this.table.data.forEach(r => {
    //     this.$refs.table.toggleRowSelection(r, false)
    //   })
    // }
  }
}



