<template>
  <div class="container-absolute"
       v-loading="saving">
    <div class="container-absolute-view">
      <div class="row-name-title">
        <div class="first-font">{{firstFont}}</div>
        <div class="name-title">{{viewData.enterpriseName}}</div>
        <el-button type="primary"
                   @click="$emit('close')">返回</el-button>
      </div>
      <el-form label-width="150px"
               ref="form"
               :model="form"
               :rules="rules">
        <el-form-item label="相关线索">
          {{viewData.relatedClues}}
        </el-form-item>
        <div class="form-flex-box">
          <el-form-item label="涉案人数"
                        prop="involvePeople"
                        :rules="peopleNumRule">
            <el-input v-model="form.involvePeople"
                      class="form-edit-content" />
          </el-form-item>
          <el-form-item label="奖励金额(万元)"
                        prop="money"
                        :rules="moneyRule1">
            <el-input v-model="form.money"
                      class="form-edit-content" />
          </el-form-item>
          <el-form-item label="员工人数"
                        prop="employeesNumber"
                        :rules="peopleNumRule">
            <el-input v-model="form.employeesNumber"
                      class="form-edit-content" />
          </el-form-item>
          <el-form-item label="集资模式"
                        prop="fundRaisingMode">
            <el-select-mine v-model="form.fundRaisingMode"
                            :options="collectModelOptions"
                            class="form-edit-content" />
          </el-form-item>
          <el-form-item label="未兑付资金">
            <el-input v-model="form.outstandingFund"
                      class="form-edit-content"
                      clearable />
          </el-form-item>
          <el-form-item label="资金清退情况(万元)"
                        prop="withdrawalFund"
                        :rules="moneyRule1">
            <el-input v-model="form.withdrawalFund"
                      :precision="6"
                      :step="0.000001"
                      size="small"
                      :min="0"
                      :controls="false"
                      class="form-edit-content" />
          </el-form-item>
          <el-form-item label="是否涉及互联网"
                        prop="involveInternet">
            <el-select-mine v-model="form.involveInternet"
                            :options="modelOptions"
                            class="form-edit-content" />
          </el-form-item>
          <el-form-item label="地域规模"
                        prop="regionalScale">
            <el-select-mine v-model="form.regionalScale"
                            :options="areaTypeOptions"
                            class="form-edit-content" />
          </el-form-item>
          <el-form-item label="涉及地区">
            <el-input v-model="form.involveArea"
                      class="form-edit-content"
                      clearable />
          </el-form-item>
        </div>
        <div class="form-flex-box">
          <el-form-item label="核查风险详情"
                        class="width-100">
            <el-input v-model="form.riskDetails"
                      type="textarea"
                      placeholder="请输入排查出的主要违法违规行为和风险点、业务模式、规模和最新处置情况等内容。"
                      resize="none"
                      :autosize="{ minRows: 2, maxRows: 4 }"
                      clearable />
          </el-form-item>
        </div>
        <div class="form-flex-box">
          <el-form-item label="行政化解方式">
            <el-form-item v-for="(item, index) in solveModelOptions"
                          :key="index"
                          label-width="120px">
              <div slot="label"
                   class="text-left">{{item.label}}</div>
              <el-date-picker v-model="form[item.value]" />
            </el-form-item>
          </el-form-item>
          <!--<el-form-item label="核查状态"-->
                        <!--label-width="80px">-->
            <!--<el-select-mine v-model="form.checkStatus"-->
                            <!--:options="checkStatusOptions"-->
                            <!--class="width-100" />-->
          <!--</el-form-item>-->
        </div>
      </el-form>
    </div>
    <div class="bottom-operate">
      <el-button type="primary"
                 @click="submit">提交</el-button>
      <el-button @click="reset">重置</el-button>
    </div>
  </div>
</template>
<script>
import * as CONSTANTS from '@/constants/common'
import { $rNumberValidate } from '@/utils/validFuns'
export default {
  name: 'CheckDisposeInspect',
  props: {
    viewData: {
      type: Object,
      required: true
    }
  },
  data() {
    // const requiredBlurRule = [
    //   {
    //     required: true,
    //     message: '不能为空',
    //     trigger: 'blur'
    //   }
    // ]
    const requiredChangeRule = [
      {
        required: true,
        message: '不能为空',
        trigger: 'change'
      }
    ]

    return {
      dialogVisible: true,
      saving: false,
      collectModelOptions: CONSTANTS.COLLECT_MODEL,
      areaTypeOptions: CONSTANTS.AREA_TYPE,
      modelOptions: CONSTANTS.MODEL,
      // checkStatusOptions: CONSTANTS.INSPECT_STATUS,
      form: {
        involvePeople: '',
        money: '',
        employeesNumber: '',
        fundRaisingMode: '',
        outstandingFund: '',
        withdrawalFund: '',
        involveInternet: '',
        regionalScale: '',

        involveArea: '',

        riskDetails: '',

        warnInterviewTime: '',
        orderRectificationTime: '',
        stopRectificationTime: '',
        freezingFundsTime: '',
        otherTime: ''

        // checkStatus: ''
      },
      peopleNumRule: $rNumberValidate({required: true, num2: 0, min: 1}),
      moneyRule1: $rNumberValidate({required: true, num2: 6}),
      // moneyRule2: $rNumberValidate({required: true, min: 0}),
      rules: {
        fundRaisingMode: requiredChangeRule,
        involveInternet: requiredChangeRule,
        regionalScale: requiredChangeRule
        // checkStatus: requiredBlurRule
      },
      solveModelOptions: CONSTANTS.SOLVE_MODEL
    }
  },
  computed: {
    firstFont() {
      return this.viewData.enterpriseName && this.viewData.enterpriseName[0]
    }
  },
  watch: {},
  created() {
    // this.form.involveArea = this.viewData.areaId
  },
  mounted() {},
  methods: {
    submit(){
      this.$refs.form.validate(valid => {
        if(valid){
          this.saving = true
          this.form.checkTime = dayjs().format('YYYY/MM/DD')
          let form = {...this.form}
          form.money = form.money * 10000
          form.withdrawalFund = form.withdrawalFund * 10000
          this.$http.put('/verifies/process/' + this.viewData.id, this.form).then(res => {
            if (res.code === this.$root.codeSuccess) {
              this.$message.success('核查成功')
              this.cancel()
              this.$emit('reload')
            }
            this.saving = false
          })
        }
      })
    },
    cancel() {
      this.$emit('close')
    },
    reset() {
      for(let key in this.form) {
        this.form[key] = ''
      }
    }
  },
  beforeDestroy() {},
  components: {}
}
</script>




