<template>
  <div>
    <div class="current-router-title">{{menuTitle}}</div>
    <div class="content-view"
         id="risk-test"
         v-loading="loading">
      <div class="search-box">
        <span class="search-title">输入企业名称，查看企业健康报告</span>
        <el-input placeholder="请输入企业名称"
                  v-model="name"
                  @keyup.native.enter="search">
          <el-button slot="append"
                     class="el-button--primary"
                     @click="search">立即查看</el-button>
        </el-input>
      </div>
    </div>
    <component :is="currentOperateView"
               :viewData="viewData"
               @close="currentOperateView=''" />
  </div>
</template>
<script>
import RiskTestView from './RiskTestView'
export default {
  name: 'RiskTest',
  mixins: [],
  props: {
    menuTitle: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      loading: false,
      name: '',
      viewData: null,
      currentOperateView: ''
    }
  },
  computed: {
    searchUrl() {
      return this.$root.isScience ? '/detect/science/' : '/detect/finance/'
    }
  },
  // watch: {},
  // created() {},
  // mounted() {
  //   this.name = '广西南宁卓信商贸有限公司'
  //   this.search()
  // },
  methods: {
    search() {
      if(this.name) {
        this.loading = true
        this.$http.get(this.searchUrl + encodeURIComponent(this.name)).then(res => {
          if(res.code === this.$root.codeSuccess) {
            if(res.content && typeof res.content === 'object') {
              this.viewData = res.content
              this.currentOperateView = 'RiskTestView'
            }else{
              this.$message.warning('未检索到该企业信息')
            }
          }
          this.loading = false
        })
      }else{
        this.$message.warning('请输入企业名称')
      }
    }
  },
  beforeDestroy() {},
  components: {
    RiskTestView
  }
}
</script>




