<template>
  <div>
    <div class="current-router-title">{{menuTitle}}
      <div class="current-router-operate">
        <el-button type="primary"
                   icon="el-icon-plus"
                   @click="editUser(null)">新增</el-button>
      </div>
    </div>
    <div class="content-view"
         v-loading="table.loading">
      <div class="table-query-container">
        <div class="table-query">
          <div class="table-query-item">
            <div class="table-query-label">姓名:</div>
            <el-input v-model="params.userName"
                      class="table-query-content"
                      clearable
                      placeholder="请输入姓名" />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">所属地区:</div>
            <el-cascader v-model="params.areaId"
                         :props="areaProps"
                         class="table-query-content"
                         filterable
                         clearable />
          </div>
          <div class="table-query-item">
            <div class="table-query-label">登陆账号:</div>
            <el-input v-model="params.loginName"
                      class="table-query-content"
                      clearable
                      placeholder="请输入登录名" />
          </div>
        </div>
        <div class="table-query-btn">
          <el-button type="primary"
                     @click="m_queryTable">查询</el-button>
          <el-button type="primary"
                     @click="m_resetTable">重置</el-button>
        </div>
      </div>
      <div class="table-container">
        <el-table :data="table.data"
                  stripe
                  border
                  style="width: 100%">
          <el-table-column type="index"
                           width="50"
                           label="序号">
          </el-table-column>
          <el-table-column prop="loginName"
                           label="登陆账号"
                           width="180">
          </el-table-column>
          <el-table-column prop="userName"
                           label="姓名"
                           width="180">
          </el-table-column>
          <el-table-column prop="phone"
                           label="手机号"
                           width="150">
          </el-table-column>
          <el-table-column prop="areaName"
                           label="所属地区"
                           show-overflow-tooltip>
          </el-table-column>
          <el-table-column width="100"
                           label="操作"
                           fixed="right">
            <template slot-scope="scope">
              <el-button type="text"
                         @click="editUser(scope.row)"
                         title="修改"
                         class="icon_button"><i class="el-icon-edit"></i></el-button>
              <el-button type="text"
                         @click="delUser(scope.row)"
                         title="删除"
                         class="icon_button"><i class="el-icon-delete"></i></el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-pagination :current-page.sync="params.pageNo"
                     :page-size.sync="params.pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :page-sizes="pageSizes"
                     :total="table.total"
                     @size-change="m_handleSizeChange"
                     @current-change="m_handleCurrentChange" />
    </div>
    <UserEdit v-if="showUserEdit"
              :viewData="viewData"
              @reload="m_tableLoadData"
              @close="showUserEdit=false" />
  </div>
</template>
<script>
  import tableQuery from '@/mixins/tableQuery'
  import UserEdit from './UserEdit'
  export default {
    name: 'UserManage',
    mixins: [tableQuery],
    props: {
      menuTitle: {
        type: String,
        required: true
      }
    },
    data() {
      let that = this
      return {
        params: {
          loginName: '',
          userName: '',
          areaId: ''
        },
        tableUrl: '/users/list',
        showUserEdit: false,
        viewData: null,

        areaProps: {
          checkStrictly: true,
          emitPath: false,
          lazy: true,
          label: 'areaName',
          value: 'id',
          lazyLoad (node, resolve) {
            let url = node.root ? '' : ('/' + node.value)
            if(node.isLeaf) {
              resolve([])
            }else{
              that.$http.get('/areas/subs' + url, '', {customMessage: true}).then(res => {
                if(res.code === that.$root.codeSuccess) {
                  res.content.forEach(a => {
                    a.leaf = a.type === that.$root.areaLeaf
                  })
                  resolve(res.content)
                }else{
                  resolve([])
                }
              })
            }
          }
        }
      }
    },
    computed: {},
    watch: {},
    created() {},
    mounted() {},
    methods: {
      editUser(row) {
        this.viewData = row
        this.showUserEdit = true
      },
      delUser(row) {
        this.$confirm('删除后该用户将无法登录系统，确定删除吗？', '删除用户').then(() => {
          this.$http.delete('/users/del/' + row.id).then(res => {
            if(res.code === this.$root.codeSuccess) {
              this.$message.success('删除成功')
              this.m_tableLoadData()
            }
          })
        })
      }
    },
    beforeDestroy() {},
    components: {
      UserEdit
    }
  }
</script>




