// 自定义校验方法
// 常量接口
export const constantApiReg = /^\/code\/[1-9]\d{7}$/

// 中文名
export const cnNameReg = /^[\u4e00-\u9fa5a-zA-Z0-9][\u4e00-\u9fa5\w]{0,29}$/
export const cnNameMsg = '中英文、数字、下划线(非开头)'

export const cnEnumNameReg = /^[^().,@#$\\]{0,30}$/
export const cnEnumNameMsg = '不支持英文的 括号 点 逗号 @ # $ \\'

export const cnNameMessage = cnNameMsg + ',长度最大30'
// 英文名 不能以数字开头
const enNameReg = /^[a-zA-Z][\w]{0,29}$/
export const enNameMsg = '英文、数字(非开头)、下划线（非开头)'
// export const enNameMessage = enNameMsg + ',长度最大30'
// 手机
const phoneReg = /^1\d{10}$/
// 密码
const passwordReg = /^[a-zA-Z0-9]{8,30}$/
// int
// export const intReg = /(^(-?[1-9]\d{0,8}|0)$)|(^([1-2]\d(?<!2[2-9])\d(?<!1[5-9])\d(?<!4[8-9])\d(?<!7[5-9])\d(?<!4[9])\d(?<!8[4-9])\d(?<!3[7-9])\d(?<!6[5-9])\d(?<!4[8])|0)$)|(^-([1-2]\d(?<!2[2-9])\d(?<!1[5-9])\d(?<!4[8-9])\d(?<!7[5-9])\d(?<!4[9])\d(?<!8[4-9])\d(?<!3[7-9])\d(?<!6[5-9])\d(?<!4[9])|0)$)/
export const intReg = {
  test: function(v) {
    let n = Math.pow(2, 31)
    return /(^-?[1-9]\d{0,9}$)|(^0$)/g.test(v) && v < n && v >= -n
  }
}
// 2147483648 -2147483647
export const intMessage = '整数,取值范围-2^31 — 2^31-1'
// long
export const longReg = /^(-?[1-9]\d{0,14}|0)$/
export const longMessage = '长整数,最大15位'
// naturalNum
export const natureNumReg = /^[1-9]\d{0,14}$/
export const naturalNumMessage = '正整数,最大15位'
// export const naturalNumMessage = '正整数,最大1999999999'
// double
export const doubleReg = /^-?(([1-9]\d{0,14})|0)(\.\d{0,16})?$/
export const doubleMessage = '数值,正负15位整数,16位小数'
// bigDecimal
export const bigDecimalReg = /^-?(([1-9]\d{0,29})|0)(\.\d{0,30})?$/
export const bigDecimalMessage = '数值,正负30位整数,30位小数'
// string
export const stringReg = /^[^\"]{1,128}$/
export const stringMessage = '不支持英文的 ",长度最大128'
// array
const arrayReg = /^[^,]+(,[^,]+)*$/
export const arrayMessage = '英文逗号分割多值'
// date
export const dateMessage = '请选择日期'
// enumString
export const enumStringReg = /^[^\"\@\#\$\.,\\]{1,128}$/
export const enumStringMessage = '不支持英文的 逗号 点 " @ $ # \\,长度最大128'
// email
const emailReg = /\w+@\w+(\.\w+)+/
const emailMessage = '不支持中文,邮箱格式xxx@xxx.xxx'

const rangeReg = /^(\(|\[).*\,.*[\)\]]$/

// date字段取值类型
const dateValueTypeMark = 3

export const ratioReg = /^[1-9]\d?$/
export const ratioMessage = '1~99 之间的正整数'
// excel正则
export const excelFileReg = /\.xlsx?$/

// 密码校验
export function $rPassword(rule, value, callback) {
  if (value) {
    if (passwordReg.test(value)) {
      let level = 0
      if (/[a-z]/.test(value)) {
        level++
      }
      if (/[A-Z]/.test(value)) {
        level++
      }
      if (/\d/.test(value)) {
        level++
      }
      if (level < 3) {
        callback(new Error('密码必须包含大小写字母、数字三种组合'))
      } else {
        callback()
      }
    } else {
      callback(new Error('密码仅支持大小写字母、数字三种组合,长度8~30'))
    }
  } else {
    callback(new Error('不能为空'))
  }
}
// 必填校验 支持 字符串数组--默认必填
export function $rRequired({ label, trigger, form = 'form', required = true }) {
  return [
    {
      validator: (rule, value, callback) => {
        if (required) {
          if (label === '菜单权限') {
            setTimeout(() => {
              value = this[form] ? this[form][rule.field] || value : value
              if ((value instanceof Array && !value.length) || !value) {
                callback(new Error('菜单权限不能为空'))
              } else {
                callback()
              }
            }, 0)
          } else {
            if ((value instanceof Array && !value.length) || !value) {
              callback(new Error((label || '') + '不能为空'))
            } else {
              callback()
            }
          }
        } else {
          callback()
        }
      },
      trigger
    }
  ]
}
// 邮箱校验
export function $rEmail({ required = false }) {
  return [
    { required, message: '请输入邮箱', trigger: 'blur' },
    { min: 0, max: 100, message: '最多100个字符', trigger: 'blur' },
    { pattern: emailReg, message: emailMessage, trigger: 'blur' }
  ]
}
// 手机校验
export function $rPhone({ required = false }) {
  return [
    { required, message: '请输入手机号', trigger: 'blur' },
    { pattern: phoneReg, message: '手机号格式不正确', trigger: 'blur' }
  ]
}
// 英文名校验 不能以数字开头
export function $rEnName({ label, checkApi, customKey, form = 'form', required = true, hasProduct = false }) {
  let rules = [
    {
      required,
      message: (label || '') + '不能为空',
      trigger: 'blur'
    },
    { min: 0, max: 30, message: '长度最大30', trigger: 'blur' },
    { pattern: enNameReg, message: enNameMsg, trigger: 'blur' }
  ]
  if (checkApi) {
    rules.push({
      validator: (rule, value, callback) => {
        const key = customKey || rule.field
        let data = {
          id: this[form].id,
          [key]: value
        }
        if (hasProduct) {
          data.productId = this[form].productId
          data.layerId = this[form].layerId
        }
        this.$http
          .post(checkApi, data, { customMessage: true })
          .then(
            res => {
              if (res.code === this.$root.codeSuccess) {
                callback()
              } else {
                callback(new Error((label || '不能') + '重复'))
              }
            },
            () => {
              callback(new Error((label || '不能') + '重复'))
            }
          )
      },
      trigger: 'blur'
    })
  }
  return rules
}
// 中文名校验
export function $rCnName({ label, checkApi, customKey, form = 'form', required = true, hasProduct = false, isCustomMessage = false, isEnumShowValue = false }) {
  let rules = [
    {
      required,
      message: (label || '') + '不能为空',
      trigger: 'blur'
    },
    { min: 0, max: 30, message: '长度最大30', trigger: 'blur' },
    { pattern: isEnumShowValue ? cnEnumNameReg : cnNameReg, message: isEnumShowValue ? cnEnumNameMsg : cnNameMsg, trigger: 'blur' }
  ]
  if (checkApi) {
    rules.push({
      validator: (rule, value, callback) => {
        const key = customKey || rule.field
        let data = {
          id: this[form].id,
          [key]: value
        }
        if (hasProduct) {
          data.productId = this[form].productId
          data.layerId = this[form].layerId
        }
        // 创建编辑组织
        // if (rule.field === 'branchName') {
        //   data.branchFId = this[form].branchFId
        //   if (!this[form].branchFId) {
        //     return callback()
        //   }
        // }
        // 创建编辑角色
        if (rule.field === 'roleName') {
          data.branchId = this[form].branchId
          if (!this[form].branchId) {
            return callback()
          }
        }
        this.$http.post(checkApi, data, { customMessage: true }).then(
          res => {
            if (res.code === this.$root.codeSuccess) {
              callback()
            } else {
              if(isCustomMessage){
                callback(new Error(res.message))
              }else{
                callback(new Error(label + '重复'))
              }
            }
          },
          () => {
            callback(new Error(label + '重复'))
          }
        )
      },
      trigger: 'blur'
    })
  }
  return rules
}
// 描述校验
export function $rDesc(required = false) {
  required = typeof required === 'object' ? required.required : required
  return [
    { required, message: '不能为空', trigger: 'blur' },
    { min: 0, max: 100, message: '长度最大100', trigger: 'blur' }
  ]
}
