// 多值 int long bigDecimal double string date 校验
const arrAmountRegMap = {
  1: {
    reg: intReg,
    message: intMessage
  },
  2: {
    reg: stringReg,
    message: stringMessage
  },
  5: {
    reg: doubleReg,
    message: doubleMessage
  },
  4: {
    reg: bigDecimalReg,
    message: bigDecimalMessage
  },
  8: {
    reg: longReg,
    message: longMessage
  }
}
export const arrAmountMsg = '英文逗号分割多值,单值:'
export function $rArrAmountValueType(required, valueType) {
  let isDate = valueType === dateValueTypeMark
  let trigger = isDate ? 'change' : 'blur'
  let rules = [{ required, message: '不能为空', trigger }]
  if (!isDate && arrAmountRegMap[valueType]) {
    let { reg, message } = arrAmountRegMap[valueType]
    if (message) {
      rules.push({
        validator: (rule, value, callback) => {
          if (required || value) {
            let arr = value.split(',')
            if (arr.some(a => !reg.test(a))) {
              callback(new Error(arrAmountMsg + message))
            } else {
              callback()
            }
          } else {
            callback()
          }
        },
        trigger
      })
    }
  }
  return rules
}
// natureNum正整数校验
export function $rNatureNum(required = false) {
  return [
    { required, message: '不能为空', trigger: 'blur' },
    { pattern: natureNumReg, message: naturalNumMessage, trigger: 'blur' }
  ]
}
// 数量校验
export function $rCount({ min = 0, max, trigger = 'change', label = '' }) {
  return [
    {
      validator: (rule, value, callback) => {
        if (value instanceof Array) {
          value = value.length
        }
        if (value >= min && value <= max) {
          callback()
        } else {
          callback(new Error(`${label}数量在 ${min} 到 ${max} 之间`))
        }
      },
      trigger: trigger
    }
  ]
}
// 参与人员数量
export function $rParticipant() {
  return $rCount({ min: 0, max: 100, trigger: 'change' })
}

// 区间校验-支持单值
// 数值区间
// rangeOnly 仅取值范围
export function $rNumRange({
  required = true,
  isInt = false,
  isLong = false,
  isDouble = false,
  isBigDecimal = false,
  rangeOnly = true
}) {
  let reg = null
  let text = null
  let message = null
  if (isInt) {
    reg = intReg
    text = '整数'
    message = intMessage
  } else if (isLong) {
    reg = longReg
    text = '长整数'
    message = longMessage
  } else if (isDouble) {
    reg = doubleReg
    text = '数值'
    message = doubleMessage
  } else if (isBigDecimal) {
    reg = bigDecimalReg
    text = '数值'
    message = bigDecimalMessage
  } else {
    reg = new RegExp('')
    text = '未定义类型'
    message = '未定义类型'
  }
  return [
    { required, message: '不能为空', trigger: 'blur' },
    {
      validator: (rule, value, callback) => {
        if (required || value) {
          if (!rangeOnly && reg.test(value)) {
            callback()
          } else if (rangeReg.test(value)) {
            let arr = value.split(',')
            if (arr.length === 2) {
              if (value.slice(0, 2) === '[,') {
                return callback(new Error('逗号左边必须为小括号'))
              }
              if (value.slice(-2) === ',]') {
                return callback(new Error('逗号右边必须为小括号'))
              }
              let num1 = arr[0].slice(1)
              let num2 = arr[1].slice(0, -1)
              if ((num1 && !reg.test(num1)) || (num2 && !reg.test(num2))) {
                return callback(new Error('逗号两边为空或' + message))
              }
              if (num1 && num2 && num1 - num2 >= 0) {
                return callback(new Error('范围由小到大'))
              }
              callback()
            } else {
              callback(new Error('英文逗号只能有一个'))
            }
          } else {
            if (/^-?\d/.test(value)) {
              callback(new Error(message))
            } else {
              callback(new Error(text + '范围,英文的中/小括号及逗号'))
            }
          }
        } else {
          callback()
        }
      },
      trigger: 'blur'
    }
  ]
}

// 比例
export function $rRatio(ratioList, ratioKey) {
  return [
    { required: true, message: '不能为空', trigger: 'blur' },
    { pattern: ratioReg, message: ratioMessage, trigger: 'blur' },
    {
      validator: (rule, value, callback) => {
        let sum = 0
        ratioList.forEach(l => {
          sum += l[ratioKey] * 1 || 0
        })
        if (ratioList.every(p => p[ratioKey])) {
          if (sum !== 100) {
            callback(new Error('比例之和必须为100'))
          } else {
            callback()
          }
        } else {
          callback()
        }
      },
      trigger: 'blur'
    }
  ]
}

// excel 文件导入
export function $rExcel(required, file, size = 2) {
  return $rFileValidate(excelFileReg, 'xls/xlsx', size, required, '', file)
}

// 版本导入
export function $rBode(required) {
  return $rFileValidate(/\.bode$/, 'bode', 1024, required, '仅支持.bode格式')
}

/**
 * 校验模型文件
 * @returns {{validator: validator}[]}
 */
export function $rModel() {
  return $rFileValidate(/\.pmml$/, 'pmml', 10)
}

// const sReg = /\s+/g
// tag 标签校验
export function $rTag() {
  return [
    {
      validator: (rule, value, callback) => {
        // // 替换连续空格为一个空格
        // value = value.map(v => v.replace(sReg, ' '))
        if (value && value.length) {
          let len = value.length
          if (len > 5) {
            callback(new Error('标签最多添加5个'))
          } else {
            let legal = true
            for (let i = 0; i < len; i++) {
              if (value[i].length > 10) {
                callback(new Error(`第${i + 1}个标签超长,最大10位`))
                legal = false
                break
              } else if (!cnNameReg.test(value[i])) {
                callback(new Error(`第${i + 1}个标签不合法,${cnNameMsg}`))
                legal = false
                break
              }
            }
            if (legal) {
              callback()
            }
          }
        } else {
          callback()
        }
      },
      trigger: 'change'
    }
  ]
}

export function $rFileValidate(reg, fileTye, size, required = true, customMessage, file) {
  return [
    {
      validator: (rule, value, callback) => {
        file = file || value[0]
        if (required || file) {
          if (!file && required) {
            callback(new Error('请选择文件'))
          } else if (!reg.test(file.name)) {
            // callback(new Error('支持' + fileTye + '格式'))
            if(customMessage){
              callback(new Error(customMessage))
            }else{
              callback(new Error('文件格式不符合要求'))
            }
          } else if (size && file.size > size * 1024 * 1024) {
            // callback(new Error('文件大小不超过' + size + 'M'))
            callback(new Error('文件大小超过导入限制'))
          } else {
            callback()
          }
        } else {
          callback()
        }
      }
    }
  ]
}

export function $rJAVAKeyWords(keyWords) {
  return [
    {
      validator: (rule, value, callback) => {
        if (value) {
          if (keyWords.includes(value)) {
            callback(new Error('不能以JAVA关键字命名'))
          } else {
            callback()
          }
        } else {
          callback()
        }
      },
      trigger: 'change'
    }
  ]
}

export function $rNumberValidate({required, num1, num2, min}) {
  return [
    {required, message: '不能为空', trigger: 'blur'},
    {
      validator: (rule, value, callback) => {
        if(/^-?[1-9]\d*(\.\d+)?$/.test(value) || /^-?0(\.\d+)?$/.test(value)) {
          if((min || min === 0) && value < min) {
            return callback(new Error('最小为' + min))
          }
          let arr = value.split('.')
          let v1 = arr[0].length
          let v2 = (arr[1] || '').length
          if(num1 && num2) {
            if(v1 > num1 || v2 > num2) {
              return callback(new Error('最多支持' + num1 + '位整数,' + num2 + '位小数'))
            }
          }
          if(num1 && num2 === 0) {
            if(v1 > num1 || v2 > num2) {
              return callback(new Error('仅支持整数,最多' + num1 + '位'))
            }
          }
          if(!num1 && num2 === 0) {
            if(v2 > num2) {
              return callback(new Error('只支持整数'))
            }
          }
          if(!num1 && num2) {
            if(v2 > num2) {
              return callback(new Error('最多支持' + num2 + '位小数'))
            }
          }
          callback()
        }else{
          callback(new Error('请输入数值'))
        }
      },
      trigger: 'blur'
    }
  ]
}



