<template>
  <div class="business-status-container">
    <div id="businessStatus" class="echarts-height"></div>
  </div>
</template>
<script>
  import * as echarts from 'echarts'
  export default {
    title: '历年新增企业数量',
    data () {
      return {
      }
    },
    mounted () {
      this.initEcharts()
    },
    methods: {
      async initEcharts () {
        let mychart = echarts.init(document.getElementById('businessStatus'))
        this.$http.get('/statistics/operatingStatus', '', {customMessage: true}).then(res => {
          if (res.code === this.$root.codeSuccess) {
            let data = []
            res.content.forEach((item) => {
              let param = {
                name: item.operatingStatus,
                value: item.num
              }
              data.push(param)
            })
            let option = {
              title: {
                text: '经营状态分布图',
                left: 'left',
                top: 10,
                textStyle: {
                  color: '#1E90FF'
                }
              },
              tooltip: {
                trigger: 'item'
              },
              legend: {
                top: 'bottom'
              },
              series: [
                {
                  type: 'pie',
                  radius: [0, 110],
                  center: ['50%', '50%'],
                  selectedMode: 'single',
                  itemStyle: {
                    borderRadius: 8
                  },
                  data: data
                }
              ]
            }
            mychart.setOption(option)
          }
        })
      }
    }
  }
</script>
<style lang="scss" scoped>
  .business-status-container {
    margin-top: 10px;
    background-color: white;
    height: 350px;
    margin-top: 10px;
    .echarts-height {
      height: 300px;
    }
  }
</style>



