<template>
  <el-dialog :title="title"
             width="450px"
             :visible.sync="dialogVisible"
             :before-close="cancel"
             v-loading="loading">
    <!-- <el-scrollbar> -->
    <el-form :model="form"
             :rules="rules"
             ref="form"
             label-width="80px">
      <el-form-item label="登陆账号"
                    prop="loginName">
        <!-- <el-input v-model="form.loginName"
                  :value="form.loginName" /> -->
        <div class="el-input el-input--small">
          <input v-model="form.loginName"
                 type="text"
                 autocomplete="off"
                 class="el-input__inner">
        </div>
      </el-form-item>
      <template v-if="isCreate">
        <el-form-item label="密码"
                      prop="password">
          <el-input v-model="form.password"
                    type="password"
                    auto-complete="new-password"
                    @dblclick.native.prevent
                    @mousedown.native="(event)=>{event.target.setAttribute('type','text')}"
                    @focus="(event)=>{event.target.setAttribute('type','password')}"
                    @blur="(event)=>{event.target.setAttribute('type','password')}" />
        </el-form-item>
        <el-form-item label="确认密码"
                      prop="confrimPassword">
          <el-input v-model="form.confrimPassword"
                    type="password"
                    auto-complete="new-password"
                    @dblclick.native.prevent
                    @mousedown.native="(event)=>{event.target.setAttribute('type','text')}"
                    @focus="(event)=>{event.target.setAttribute('type','password')}"
                    @blur="(event)=>{event.target.setAttribute('type','password')}" />
        </el-form-item>
      </template>
      <el-form-item label="用户姓名"
                    prop="userName">
        <!-- <el-input v-model="form.userName"
                  :value="form.userName" /> -->
        <div class="el-input el-input--small">
          <input v-model="form.userName"
                 type="text"
                 autocomplete="off"
                 class="el-input__inner">
        </div>
      </el-form-item>
      <el-form-item label="手机号"
                    prop="phone">
        <!-- <el-input v-model="form.phone"
                  :value="form.phone" /> -->
        <div class="el-input el-input--small">
          <input v-model="form.phone"
                 type="text"
                 autocomplete="off"
                 class="el-input__inner">
        </div>
      </el-form-item>
      <template v-if="isCreate">
        <el-form-item label="所在区"
                      prop="areaId">
          <el-select-mine v-model="form.areaId"
                          :optionsKey="{
                            label: 'areaName',
                            value: 'id'
                          }"
                          :options="areaList"
                          @change="getArea" />
        </el-form-item>
        <el-form-item label="所在街道"
                      prop="streetId">
          <el-select-mine v-model="form.streetId"
                          :optionsKey="{
                            label: 'areaName',
                            value: 'id'
                          }"
                          :options="streetList"
                          clearable />
        </el-form-item>
      </template>
      <el-form-item v-else
                    label="所在地区">
        {{viewData.areaName}}
      </el-form-item>
    </el-form>
    <!-- </el-scrollbar> -->
    <div slot="footer">
      <el-button @click="reset">重置</el-button>
      <el-button type="primary"
                 @click="submit"
                 :loading="saving">提交</el-button>
    </div>
  </el-dialog>
</template>
<script>
export default {
  name: 'UserEdit',
  props: {
    viewData: {
      type: Object,
      default: null
    }
  },
  data() {
    const that = this
    const requiredBlurRule = [
      {
        required: true,
        message: '不能为空',
        trigger: 'blur'
      }
    ]
    const requiredChangeRule = [
      {
        required: true,
        message: '不能为空',
        trigger: 'change'
      }
    ]
    const passwordRule = [
      {
        required: true,
        message: '不能为空',
        trigger: 'blur'
      },
      {
        pattern: /^\w{8,16}$/,
        message: '密码必须为8-16为数字、字母或下划线',
        trigger: 'bulr'
      }
    ]
    return {
      dialogVisible: true,
      saving: false,
      loading: false,
      form: {
        loginName: '',
        password: '',
        confrimPassword: '',
        userName: '',
        phone: '',
        areaId: '',
        streetId: ''
      },
      editKeys: ['loginName', 'userName', 'phone'],
      rules: {
        loginName: requiredBlurRule,
        password: passwordRule,
        confrimPassword: [
          // ...passwordRule,
          {
            validator: (rule, value, callback) => {
              if(value === that.form.password) {
                callback()
              }else{
                callback(new Error('两次密码必须一致'))
              }
            },
            trigger: 'blur'
          }
        ],
        userName: requiredBlurRule,
        phone: [
          {
            pattern: /^1\d{10}$/,
            message: '手机格式不正确',
            trigger: 'blur'
          }
        ],
        areaId: requiredChangeRule
      },
      areaList: [],
      streetList: []
    }
  },
  computed: {
    isCreate() {
      return !this.viewData
    },
    title() {
      return this.viewData ? '修改用户' : '新建用户'
    },
    method() {
      return this.viewData ? 'put' : 'post'
    }
  },
  created() {
    this.getArea()
    if(this.viewData) {
      this.editKeys.forEach(k => {
        this.form[k] = this.viewData[k]
      })
      // this.getUserInfo()
    }
  },
  methods: {
    // getUserInfo() {
    //   this.loading = true
    //   this.$http.get('/users/view/' + this.viewData.id).then(res => {
    //     if(res.code === this.$root.codeSuccess) {
    //       // this.
    //     }
    //     this.loading = false
    //   })
    // },
    getArea(parentId) {
      let url = parentId ? ('/' + parentId) : ''
      this.$http.get('/areas/subs' + url, '', {customMessage: true}).then(res => {
        console.log(res)
        if(res.code === this.$root.codeSuccess) {
          if(parentId) {
            this.streetList = res.content
          }else{
            this.areaList = res.content
            this.streetList = []
          }
          this.form.streetId = ''
        }
      })
    },
    reset() {
      if(this.viewData) {
        this.editKeys.forEach(k => {
          this.form[k] = ''
        })
      }else{
        for(let key in this.form) {
          this.form[key] = ''
        }
      }
    },
    submit() {
      this.$refs.form.validate(valid => {
        if(valid) {
          this.saving = true
          let form = {...this.form}
          if(form.streetId) {
            form.areaId = form.streetId
          }
          delete form.confrimPassword
          delete form.streetId
          let method
          let url
          if(this.viewData) {
            method = 'put'
            url = 'update'
            form.id = this.viewData.id
            delete form.password
            delete form.areaId
          }else{
            method = 'post'
            url = 'add'
          }
          this.$http[method]('/users/' + url, form).then(res => {
            this.saving = false
            if(res.code === this.$root.codeSuccess) {
              this.$emit('reload')
              this.cancel()
            }
          })
        }
      })
    },
    cancel() {
      this.$emit('close')
    }
  },
  beforeDestroy() {},
  components: {}
}
</script>



