import Vue from 'vue'
import Router from 'vue-router'
import mainMenu from './main'

Vue.use(Router)

export let MenuList = [
  {
    path: '/',
    redirect: '/login'
  },
  ...mainMenu,
  /**
   * 请放在路由的最后一项,请放在路由的最后一项,请放在路由的最后一项
   * 调试组件页- npm rum build 时不需要
   */
  {
    path: '/demo',
    name: 'Demo',
    component: () => import(`@/views/Demo.vue`)
  }
]

// npm run build
if(process.env.NODE_ENV === 'production') {
  MenuList.pop()
}

const router = new Router({
  routes: MenuList
  // scrollBehavior (to, from, savedPosition) {
  //   console.log(to, from)
  //   // return 期望滚动到哪个的位置
  //   return { x: 0, y: 0 }
  // }
})

// router.beforeEach((to, from, next) => {
//   next()
// })

export default router



